<template lang="en">
    
    <transition name="modal">
        <!-- The Modal -->
        <div class="modal my-popups d-block" id="myModal">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">{{$t('Location')}}</h4>
                        <button type="button" v-on:click="$emit('close')" class="close" data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <!-- Modal body -->
                    <div class="modal-body">
                        <div class="col-md-12">
                            <div class="table-responsive">
                                <table id="tableshowlocationandmapdata" class="table table-bordered nowrap mt-0">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th scope="col"><span>{{$t('Location')}}</span></th>
                                            <th scope="col">{{$t('TrackTime')}}</th>
                                            <th scope="col">{{$t('IpAddress')}} </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item,index) in TrackLocationData">
                                            <td>{{item.LOCATION}}</td>
                                            <td>{{FormatDateToTime(item.Tracking_Time,"MM/DD/YYYY hh:mm a")}}</td>
                                            <td>{{item.IP_ADDRESS}}</td>
                                        </tr>
                                        <tr v-if="noRecord">
                                            <td colspan="3" class="text-center text-danger no-record">{{$t('NoRecordfound')}}</td>
                                        </tr>
                                    </tbody>
                                </table>
                                <!--modal for quick view-->
                            </div>
                        </div>
                        <a v-if="!noRecord" v-for="(item,index) in TrackLocationData" id="showMap" v-on:click="showMap(item, index)" class="btn btn-primary text-white float-right m-3" title="View"><i class="fa fa-map-marker pr-1" aria-hidden="true"></i>{{$t('ViewMap')}}</a>
                        <div id="map-canvas" style="height:400px; border:5px solid #cccccc;" class="d-none col-12 float-left">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<!-- <script type="text/javascript" src="https://maps.googleapis.com/maps/api/js?key=AIzaSyAg_CHb-HKZvn-lZ1hHfVg3dN1WNOQVp9U&sensor=false"></script> -->
<script>
import DataService from '../../services/DataService';
import moment from "moment"
export default {
   
  data() {
            return {
                TrackLocationData:[],
                noRecord:false,
                Latitude: '',
                Longitude: '',
                MapIndex: null,
                CurrentItem: null,
            }
        },
       // mixins: [commonMixin],
        created: function () {
            ;
            this.showLocation();
            let GoogleMapScript = document.createElement("script");
        GoogleMapScript.setAttribute(
          "src",
          "https://maps.googleapis.com/maps/api/js?key=AIzaSyAJdx4gmhGKWjPTBBVk5riFgpbE0lGS-xM&libraries=geometry,drawing,places&v=weekly"
        );
        document.head.appendChild(GoogleMapScript);
        },
        methods: {
             showLocation: function(){
                var vm = this;
                
                var params = "trackId="+vm.$parent.TrackId;                
                 DataService.GetShowLocationsAndMap(params).then(function (response) {
                    vm.isLoading = false;
                    console.log(response);
                    if (response.data != null && response.data != "") {

                        vm.TrackLocationData = response.data.result.data;
                        if (vm.TrackLocationData.length > 0) {
                            vm.noRecord = false;
                        }
                        else{
                            vm.noRecord = true;
                        }
                    }
                    else {
                        vm.noRecord = true;
                    }
                    // setTimeout("CheckBoxBootstrap()", 100);
                    // setTimeout("ShowfileUploadWithCulture();", 550);
                });
            },
            showMap: function (item, index) {

                var vm = this;
                var map = new google.maps.Map(document.getElementById("map-canvas"), {
                    zoom: 7,
                    zoomControl: true,
                    mapTypeControlOptions: {
                        style: google.maps.MapTypeControlStyle.HORIZONTAL_BAR,
                        position: google.maps.ControlPosition.BOTTOM_RIGHT
                    },
                    zoomControlOptions: {
                        position: google.maps.ControlPosition.RIGHT_CENTER,
                    },
                    center: new google.maps.LatLng(30.7333,76.7794),
                    mapTypeId: google.maps.MapTypeId.ROADMAP,
                });
                if (item != null && item != '') {
                    $("#map-canvas").removeClass("d-none");
                    if (!this.IsObject(item)) {
                        item = JSON.parse(item);
                    }
                    
                    vm.CurrentItem = item;
                    vm.Latitude = item.LATITUDE
                    vm.Longitude = item.LONGITUDE
                    vm.MapIndex = index;
                    //vm.initialize(vm.Latitude,vm.Longitude,item);
                }
            },
            split:function (val) {
                return val.split(/,\s*/);
            },
            initialize: function (lat,long,item) {
                
                var myLatlng = new google.maps.LatLng(lat, long);
                var mapOptions = {
                    center: myLatlng,
                    zoom: 6,
                    mapTypeId: google.maps.MapTypeId.ROADMAP
                };
                var marker = new google.maps.Marker({
                    position: myLatlng,
                    map: map,
                    title: "Hello!"
                });
                var LocationCoordinatesArray = new Array();

                for ( let i = 0; i < item.length; i++) {

                    var point = new google.maps.LatLng(item[i].LATITUDE, item[i].LONGITUDE);
                    LocationCoordinatesArray.push(point);
                }
                var flightPath = new google.maps.Polyline({
                    path: LocationCoordinatesArray,
                    geodesic: true,
                    strokeColor: '#FF0000',
                    strokeOpacity: 1.0,
                    strokeWeight: 2
                });

                for (let i = 0; i < LocationCoordinatesArray.length; i++) {
                    var marker = new google.maps.Marker({
                        position: new google.maps.LatLng(LocationCoordinatesArray[i].lat, LocationCoordinatesArray[i].lon),
                        map: map,
                        title: "Hello!"
                    });
                    var map = new google.maps.Map($('#map-canvas')[0], mapOptions);
                    flightPath.setMap(map);
                    vm.setMarkers(map, LocationCoordinatesArray);
                }

            },
            setMarkers: function (map, locations) {

                var shape = {
                    coord: [1, 1, 1, 20, 18, 20, 18, 1],
                    type: 'poly'
                };
                for (var i = 0; i < locations.length; i++) {
                    var myLatLng = new google.maps.LatLng(locations[i].split(',')[0], locations[i].split(',')[1]);
                    var marker = new google.maps.Marker({
                        position: myLatLng,
                        map: map,
                        icon: '../../Content/images/beachflag.jpg',
                        shape: shape,
                        visible: true
                    });
                }
            },
            FormatDateToTime(Getdate,formattype) {
            return moment(Getdate).format(formattype);
        }
        }
    }
</script>