<template>
   <div class="modal my-popups right fade" data-keyboard="false" data-backdrop="static"
      id="modalprojectquickviewpopaddreport" role="dialog" v-cloak>
      <div class="modal-dialog" role="document">
         <div class="modal-content">
            <div class="modal-header">
               <button type="button" style="height: 56px;" class="close" v-on:click="CloseReportView()"
                  data-dismiss="modal"><i class="fa fa-times" aria-hidden="true"></i></button>
               <h4 class="modal-title text-truncate" style="line-height: 26px;" id="myModalLabel">Add Report</h4>
            </div>
            <div class="modal-body">
               <div class="container-fluid p-0">
                  <div id="accordionEx" class="accordion md-accordion left-filter">
                     <div class="border bg-light">
                        <!-- <div class="card mb-2 p-2">
                           <div role="tab" id="MainColumn0" class="card-header heading-border p-0">
                              <h5 class="mb-0">
                                 <a data-toggle="collapse" data-parent="#accordionEx" data-target="#ColumnCollapse0"
                                    href="javascript:;" aria-expanded="true" aria-controls="ColumnCollapse0"
                                    class="d-block">
                                    <span class="Textstyle"> System Report</span><em aria-hidden="true"
                                       class="fa fa-angle-down rotate-icon"></em>
                                 </a>
                              </h5>
                           </div>
                           <div class="card" v-for="(option, index) in SystemReport" :key="option.privilege_id">
                              <div class="card-header">
                                 <div :id="'systemId' + index" class="d-flex justify-content-between align-items-center">
                                    <div class="d-flex align-items-center">
                                       <input type="checkbox" class="chkItems" :data-reportid="option.privilege_id"
                                          :data-chartType="option.chart_type == null ? 0 : option.chart_type"
                                          :checked="reportbydashbord && reportbydashbord.some(ele => ele.reportId == option.privilege_id)" />
                                       <div class="ml-2">{{ $t(option.privilege_name) }}</div>
                                    </div>
                                    <span v-if="option.privilege_type === 'WIDGET_MAX'"
                                       style="font-weight: normal; padding: 5px; line-height: 10px;"
                                       class="badge badge-danger float-right">Graph Widget</span>
                                    <span v-else style="font-weight: normal; padding: 5px; line-height: 10px;"
                                       class="badge badge-success float-right">Numeric Widget</span>
                                 </div>
                              </div>
                           </div>

                        </div> -->
                        <div class="col-12 p-3">
                           <div role="tab" id="MainColumn1" class="card-header heading-border p-0">
                              <h5 class="mb-0">
                                 <a data-toggle="collapse" data-parent="#accordionEx" data-target="#ColumnCollapse1"
                                    href="javascript:;" aria-expanded="true" aria-controls="ColumnCollapse1"
                                    class="d-block">
                                    <span class="Textstyle p-0">Reports</span><em aria-hidden="true"
                                       class="fa fa-angle-down rotate-icon"></em>
                                 </a>
                              </h5>
                           </div>
                           <div id="ColumnCollapse1" role="tabpanel" aria-labelledby="MainColumn1"
                              data-parent="#accordionEx" class="multi-collapse collapse show" style="">
                              <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 mt-4">
                                 <div id="accordion" role="tablist" class="accordion md-accordion w-100"
                                    v-if="reportGroups">
                                    <div class="cardtopbod bg-light">
                                       <div class="card" v-for="(reportGroup, index) in reportGroups" :key="index">
                                          <div class="card-header">
                                             <a class="card-link collapsed" data-toggle="collapse" data-parent="#accordion"
                                                :href="'#collapseOne' + index" aria-expanded="true"
                                                :data-target="'#collapseOne' + index"
                                                :aria-controls="'collapseOne' + index">
                                                <div class="">
                                                   <span><em class="fa fa-folder pr-1" aria-hidden="true"></em>{{
                                                      reportGroup.reports[0].folderName }} </span>
                                                </div>
                                             </a>
                                          </div>
                                          <div :id="'collapseOne' + index" class="collapse" role="tabpanel"
                                             data-parent="#accordion" style="">
                                             <div class="card-body">
                                                <div class="row">
                                                   <div class="col-md-12 listing">
                                                      <div
                                                         class="table-responsive table-fix-header dynamic-dashboard-add-report-table">
                                                         <table :id="'tblReport' + index"
                                                            class="table table-bordered mt-0">
                                                            <thead class="thead-bg">
                                                               <tr>
                                                                  <th class="text-center">
                                                                     <div class="custom-control custom-checkbox">
                                                                        <input data-v-765974b3=""
                                                                           v-on:change="selectAllCheckBox($event, reportGroup)"
                                                                           :checked="reportGroup.isAllSelected"
                                                                           type="checkbox"
                                                                           class="chkItems1 custom-control-input"
                                                                           style="display: none;" />
                                                                        <label
                                                                           class="custom-control-label universal-custom-control-label pt-1"
                                                                           for="chkAll_20"></label>
                                                                     </div>
                                                                  </th>
                                                                  <th>Report Name</th>
                                                                  <th v-if="moduleCode == 'Home'">Module Name</th>
                                                                  <th>Type</th>
                                                                  <th>View Type</th>
                                                               </tr>
                                                            </thead>
                                                            <tbody>
                                                               <tr v-for="(report, reportIndex) in reportGroup.reports"
                                                                  :key="reportIndex">
                                                                  <td class="text-center">
                                                                     <input class="chkItems"
                                                                        v-on:change="selectCheckBox($event, reportGroup)"
                                                                        :data-reportid="report.reportId"
                                                                        :data-chartType="report.chartType == null ? 0 : report.chartType"
                                                                        type="checkbox"
                                                                        :checked="reportbydashbord && reportbydashbord.some(ele => ele.reportId == report.reportId)" />
                                                                  </td>
                                                                  <td>{{ report.reportName }}</td>
                                                                  <td v-if="moduleCode == 'Home'">{{ report.moduleName }}
                                                                  </td>
                                                                  <td>
                                                                     <span
                                                                        v-if="(report.chartType === 'Pie') || (report.chartType === 'Bar') || (report.chartType === 'Donut') || (report.chartType === 'Line') || (report.chartType === 'Horizontal_Bar') || (report.chartType === 'Multiple_Bar')"
                                                                        style="font-weight: normal; padding: 5px; line-height: 10px;"
                                                                        class="badge badge-danger float-right">Graph
                                                                        Widget</span>

                                                                     <span v-else-if="report.chartType === 'Numbers_Chart'"
                                                                        style="font-weight: normal; padding: 5px; line-height: 10px;"
                                                                        class="badge badge-success float-right">Numeric
                                                                        Widget</span>

                                                                     <span v-else
                                                                        style="font-weight: normal; padding: 5px; line-height: 10px; color: white;"
                                                                        class="badge badge-warning float-right">Table
                                                                        Widget</span>
                                                                  </td>
                                                                  <td>


                                                                     <label
                                                                        :class="{ 'newswitch_toggle switch auto': true, 'disabled': report.chartType == 'Numbers_Chart' }"
                                                                        v-if="report.chartType != 'Table'">
                                                                        <input
                                                                           :id="'chartType' + reportIndex + '_' + index"
                                                                           class="form-check-input rdoticketfor custom-control-input dynamic"
                                                                           type="checkbox" v-bind:name="'viewtype'"
                                                                           v-bind:checked="reportbydashbord && reportbydashbord.some(ele => ele.reportId == report.reportId && ele.chartType == 'Table') ? false : true"
                                                                           v-bind:value="reportbydashbord && reportbydashbord.some(ele => ele.reportId == report.reportId) ? reportbydashbord.find(ele => ele.reportId == report.reportId).chartType : report.chartType ? report.chartType : 'Table'" />
                                                                        <span class="slider round">
                                                                           <span class="slider-yes">Graph</span>
                                                                           <span class="slider-no">Table</span>
                                                                        </span>
                                                                     </label>
                                                                  </td>
                                                               </tr>
                                                            </tbody>
                                                         </table>
                                                      </div>
                                                   </div>
                                                </div>
                                             </div>
                                          </div>
                                       </div>
                                    </div>
                                 </div>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
               </div>
               <div class="my-3">
                  <a href="javascript:;" data-tour="step: 1; title: enter DealDetail; content: AddDealDetailContent"
                     class="btn btn-success float-right" title="Save" v-on:click="saveReport()"><em
                        class="fa fa-save pr-2"></em>Save </a>
               </div>
            </div>
         </div>
      </div>
      <loader v-if="isLoading"></loader>
   </div>
</template>

<script>
import DataService from '../../services/DataService';
export default {
   props: {
      dashboardid: null,
      moduleCode: null
   },
   data: function () {
      return {
         isLoading: false,
         reportGroups: [],
         SystemReport: [],
         filteredObjects: [],
         idsString: null,
         SystemReportFiltered: [],
         reportbydashbord: null,
         FolderNames: [],
      }
   },
   created: async function () {
      await this.GetDynamicReport();


      // setTimeout(() => {
      //    debugger


      //    this.FolderNames.forEach((Mitm, Mindx) => {

      //       var Name = this.FolderNames[Mindx];

      //       var array = document.getElementsByClassName('chkItems inner' + Name);

      //       array.forEach((itm, indx) => {

      //          if (array[indx].type == "checkbox") {
      //             if (array[indx].checked) {
      //                var innerarray = document.getElementsByClassName('chkItems outer' + Name);

      //                if (innerarray[0].type == "checkbox") {
      //                   innerarray[0].checked = true;
      //                }

      //             } else {
      //                var innerarray = document.getElementsByClassName('chkItems outer' + Name);

      //                if (innerarray[0].type == "checkbox") {
      //                   innerarray[0].checked = false;
      //                }

      //             }
      //          }

      //       });



      //    });


      // }, 2000);

   },
   mounted() {
      // eslint-disable-next-line no-undef
      $("#modalprojectquickviewpopaddreport").modal('show');
      this.CheckBoxBootstrap();
   },
   methods: {
      selectAllCheckBox: function (e, ths) {

         if (e.target.checked) {
            $(e.target).closest("table").find("tbody tr td:first-child input.custom-control-input").prop("checked", true);
         } else {
            $(e.target).closest("table").find("tbody tr td:first-child input.custom-control-input").prop("checked", false);
         }
         ths.isAllSelected = e.target.checked;
      },
      selectCheckBox: function (event, item) {


         var TotalLength = $(event.target).closest("table").find("tbody tr td:first-child input.custom-control-input").length;

         var checkedLength = $(event.target).closest("table").find("tbody tr td:first-child input.custom-control-input:checked").length;

         if (TotalLength == checkedLength) {
            item.isAllSelected = true;
         }
         else {
            item.isAllSelected = false;
         }


      },
      SelectSystemReports(event) {
         const clickedLabel = event.currentTarget;
         clickedLabel.classList.toggle('active');
      },
      CloseReportView: function () {

         var vm = this;
         // $("#modalprojectquickviewpopaddreport").removeClass('show');
         vm.$parent.showAddReport = false;
      },

      GetDynamicReport: async function () {

         var vm = this;
         var param = `moduleName=${this.moduleCode}&dashboardId=${vm.dashboardid}`;
         await DataService.GetDynamicReport(param).then(function (response) {
            var data = response.data.UserReport;
            data.forEach((x) => { x.isAllSelected = false });
            vm.reportGroups = data;

            // vm.reportGroups.forEach((Mitm, Mindx) => {

            //    vm.FolderNames.push(vm.reportGroups[Mindx].reports[0].folderName);

            // });


            setTimeout(() => {

               vm.CheckBoxBootstrap();
               vm.GetDashboardReport()
            }, 1000);
         })
      },
      GetDashboardReport: async function () {

         var vm = this;
         var param = `dashboardId=${vm.dashboardid}&moduleName=${vm.moduleCode}`
         await DataService.GetDashboardReport(param).then(function (response) {
            vm.reportbydashbord = response.data;

            vm.reportGroups.forEach(function (grp) {
               var allselected = true;
               grp.reports.forEach(function (report) {
                  if (!vm.reportbydashbord.some(ele => ele.reportId == report.reportId)) {
                     allselected = false;
                  }
               });
               grp.isAllSelected = allselected;
            })
            response.data.forEach(function (obj) {
               var privilegeId = obj.reportId;
               var element = document.getElementById(privilegeId);
               if (element) {
                  element.classList.add('active');
               }
            });
         })
      },
      filterObjects() {

         var vm = this;
         vm.idsToFilter = vm.idsString.split(",");
         vm.filteredObjects = vm.SystemReportFiltered.filter((obj) =>
            vm.idsToFilter.includes(obj.privilege_id.toString())
         );
         return vm.filteredObjects;
      },
      saveReport: function () {

         var vm = this;
         // this.isLoading=true;
         var reportArr = [];
         vm.SystemReportFiltered = vm.SystemReport;
         const elementsWithSystemReportClass = document.querySelectorAll('.SystemReport');
         const elementsWithBothClasses = Array.from(elementsWithSystemReportClass).filter(element => {
            return element.classList.contains('active');
         });
         const ids = elementsWithBothClasses.map(element => element.id);
         vm.idsString = ids.join(',');
         var finlobjre = vm.filterObjects();
         for (const key in finlobjre) {
            reportArr.push({
               reportId: finlobjre[key].privilege_id,
               chartType: finlobjre[key].chart_type,
               displayOrder: key,
               xaxis: 0,
               yaxis: 0,
               is_system_report: 1
            })
         }
         // for (let index = 0; index < vm.SystemReport.length; index++) {
         //    // eslint-disable-next-line no-undef
         //    $("#systemId" + index + " .chkItems:checked")
         //       .not("[id^='chkAll']")
         //       .each(function () {
         //          // eslint-disable-next-line no-undef
         //          if ($(this).attr("data-reportid")) {
         //             reportArr.push({
         //                // eslint-disable-next-line no-undef
         //                reportId: $(this).attr("data-reportid"),
         //                // eslint-disable-next-line no-undef
         //                chartType: $(this).attr("data-chartType"),
         //                displayOrder: index,
         //                xaxis: 0,
         //                yaxis: 0,
         //                is_system_report: 1,
         //             });
         //          }
         //       });
         // }
         for (let index = 0; index < vm.reportGroups.length; index++) {
            // eslint-disable-next-line no-undef       
            $('#tblReport' + index + ' .chkItems:checked').not("[id^='chkAll']").each(function () {
               // eslint-disable-next-line no-undef        
               if ($(this).attr("data-reportid")) {
                  reportArr.push({
                     // eslint-disable-next-line no-undef
                     reportId: $(this).attr("data-reportid"),
                     // eslint-disable-next-line no-undef
                     chartType: $(this).parent().parent().parent().find('.rdoticketfor').is(":checked") ? 'Graph' : 'Table',
                     displayOrder: index,
                     xaxis: 0,
                     yaxis: 0,
                     is_system_report: 0
                  })
               }
            });
         }
         if (reportArr.length >= 2) {
            var data = {
               moduleName: 'CRM',
               reports: reportArr,
               dashboardId: vm.dashboardid,
            };
            var objectPostString = JSON.stringify(data);
            DataService.SaveReport(objectPostString).then(function (response) {
               if (response.data.status == "Success") {
                  vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                  vm.$parent.GetDashboard(vm.dashboardid);
                  vm.GetDashboardReport();
                  // eslint-disable-next-line no-undef
                  // vm.$parent.showAddReport = false;
                  var getDiv = document.getElementsByClassName("modal-backdrop fade show");

                  while (getDiv.length > 0) {
                     getDiv[0].remove();
                  }
                  vm.CloseReportView();
                  // $("#modalprojectquickviewpopaddreport").removeClass('show');
               }
               else {
                  vm.ShowAlert(vm.$t('error', [vm.$t('Report')]), "warning", true, vm.$t("Alert"));
               }
               vm.isLoading = true;
            });
         } else {
            vm.ShowAlert(
               vm.$t("ReportSelectionRequired", [vm.$t("Dashboard")]),
               "warning",
               true,
               vm.$t("Alert")
            );
         }



      }
   }
}
</script>
<style scoped>
.accordion>.card>.card-header {
   border-radius: 0;
   margin-bottom: 10px;
}

.dynamic-dashboard-add-report-table input:checked+.slider:before {
   left: 44px !important;
}

.custom-reports-icons {
   min-width: inherit;
   height: 104px;
   width: 33%;
}

.Textstyle {
   font-size: 1.25rem;
   line-height: 40px;
   text-transform: capitalize;
}</style>