<template>
  <div class="w-100 bg-white custom-chart-div t-min-height Piechrt text-center"
    :style="'height: ' + outerDivHeight + 'px;'">
    <div class="graph-widget-content bg-white float-left w-100" :style="'height: ' + outerDivHeight + 'px;'">
      <loader :isVisible="isLoading"></loader>
      <template v-if="PieWidgetData.length === 0 && isLoading === false">
        <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }"
          style="display: flex; align-items: center; justify-content: center">
          {{ $t("NoRecordfound") }}
        </div>
      </template>
      <template v-else>
        <div :id="'pieDiv_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index"
          :style="'height: ' + outerDivHeight + 'px;'">
          <VueApexCharts type="pie" :options="chartOptions" height="100%" :series="series"></VueApexCharts>
          <!-- <div style="font-size: 14px; color: #000000">{{xaxisAliasName}}</div> -->
          <div><h6>{{ $t(xaxisAliasName)}}</h6></div>
          
        </div>
      </template>
    </div>
  </div>
</template>
<script>

import DataService from "../../../services/DataService";
import VueApexCharts from "vue-apexcharts";
export default {
  components: {
    VueApexCharts
  },
  props: {
    reportName: null,
    chartType: String, // Set the type
    // selectedChartType: String,
    dashboardid: Number,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    filterData: "",
    index: null,
    width: null,
    height: null,
    reportId: null,
    resizeheight: null,
    isSystemReport: null,
    xaxisAliasName: null,
    yaxisAliasName: null
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "pie",
      PieWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      chart: null, // Add a chart property to store the C3.js chart instance
      outerDivHeight: 0,
      series: [],
      labels: [],
      chartOptions: {},
      chart: {
        type: 'pie',
      },
      responsive: [{
        breakpoint: 480,
        options: {
          chart: {
          },
          legend: {
            position: 'bottom'
          }
        }
      }]
    };
  },
  watch: {
    width: function () {
      this.bindWidget(this);
    },
    height: function () {
      this.bindWidget(this);

    },
    toDate: function () {
      this.FetchData();
    },
    fromDate: function () {
      this.FetchData();
    },
    filterData: function () {
      this.FetchData();
    },
    resizeheight: function () {

      //this.bindWidget(this)
      var h = document.getElementById('pieDiv_' + this.moduleCode + '_' + this.action.replace(/\s+/g, '_') + this.index)
      this.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
    }
  },
  created: function () {
    this.FetchData();
  },
  methods: {
    ViewReport(reportId) {
      const encodedReportID = btoa(JSON.stringify({ ReportID: reportId }));
      const route = '/Report/viewcustomreport/' + encodedReportID;
      // Open the route in a new tab
      window.open(route, '_blank');
    },
    FetchData: function () {

      var vueObj = this;
      vueObj.isLoading = true;
      // var params = `moduleName=${vueObj.moduleName}&reportName=Lead_By_Industry&fromDate=2023-03-20&toDate=2023-03-26&usersDash=`;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
      DataService.GetDashboardData(params).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            // if (vueObj.widgetInfo.charT_TYPE == "table" && response.data.result != null) {
            //     vueObj.HeaderData = response.data.result.headers;
            // }
            if (response.data.result != null) {
              vueObj.PieWidgetData = response.data.result.data;
              vueObj.bindWidget(vueObj);

            }
          }
        }
        var h = document.getElementById('pieDiv_' + vueObj.moduleCode + '_' + vueObj.action.replace(/\s+/g, '_') + vueObj.index)
        vueObj.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
        vueObj.$emit("set-widget-data", vueObj.PieWidgetData, vueObj.charT_TYPE);
        vueObj.isLoading = false;
      });
    },
    bindWidget: function (vueObj) {
      
      var vm = this;
      vm.series = [];
      vm.labels = [];
      if (vueObj.charT_TYPE && vueObj.PieWidgetData.length > 0) {

        $.each(vueObj.PieWidgetData, function (index, element) {


          vm.series.push(element.VALUE);
          vm.labels.push(element.NAME);
        });

        vm.chartOptions = {
          labels: vm.labels,
          chart: vm.chart,
          responsive: vm.responsive,
          legend: {
            position: 'bottom'
          },       
          
        }


      }
    }
  }

};
</script>



