<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
       <div class="modal fade dialog my-popups ui-draggable show" style="height: auto; width: auto; display: block; top: 0px; left: 0px;" data-keyboard="false" data-backdrop="static" role="dialog">
            <div role="document" class="modal-dialog maxwidth500">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 id="myModalLabel" class="modal-title text-truncate pb-1">{{$t('UploadFile')}} </h5>
                        
                        <button type="button" data-dismiss="modal" class="close" v-on:click="closePopup"><i aria-hidden="true"
                                class="fa fa-times"></i></button>
                    </div>
                    <div class="modal-body">
                      <span class="user-guide">
                            <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button" aria-expanded="false" aria-controls="collapseExample">{{$t('UserGuide')}}</a>
                        <div id="collapseReply" class="divancuserguide collapse">
                        <div id="scrollbarreplyuserguide" class="custom-scrollbar-js" style="overflow: auto;">
                        <div class="con" v-html="$t('User_Guide_Quick_View_Add_File')" ></div>
                        </div>
                        </div>
                        </span>
                        <dynamic-form lang="en" ref="modalAddFile" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-AttachmentFile="{ data }">
                                <!-- <a class="question-circle" href="javascript:;" data-toggle-tooltip="tooltip"
                                    :title="'Maximum upload size per file: ' + FileSizeAllowedToolTip + 'MB Allowed File Types: ' + FileTypeAllowedToolTip">
                                    <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                                </a> -->
                                                        
                            </template>
                            

                            
                            <template v-slot:tgslot-CheckBoxMultiple="{ data1 }">
                                 <div class="col-md-12 col-lg-12 col-xl-12 float-left p-0 mb-2 lastchild-control" style="max-height: 450px; overflow-y: auto; border-bottom: 1px solid #dee2e6 !important; border-top: 1px solid #dee2e6 !important; ">
                                    <div  v-for="(userlist, indexuserlist)  in GetUserList" :key="indexuserlist">
                                        <div class="form-group row m-0 border p-2 bg-f5f5f5">

                                        
                                            <div class="form-check form-check-inline">
                                                                <div class="custom-control custom-checkbox">
                                                                    <input type="checkbox"
                                                                        :class="{ 'chkItems custom-control-input': true }"
                                                                        
                                                                        :id="'chk_' + userlist.userTypeId" v-bind:checked="checkBoxUserType && selectedUserTypeId==userlist.userTypeId"    @change="CheckUserTypeAll($event,userlist)" style="display: none;">
                                                                    <label
                                                                        class="custom-control-label universal-custom-control-label pt-1"
                                                                        :for="'chk_' + userlist.userTypeId"> {{ userlist.userType }} </label>
                                                                </div>

                                                    </div>
                                        </div>  
                                        <ul :id="userlist.userTypeId"  class="row m-0 p-0 py-3 mb-3 border border-top-0">
                                            <li class="col-sm-12 col-md-4 col-lg-3 col-xl-3 float-left pl-2 mb-2" style="list-style:none;"   v-for="(inneruser, indexinneruser)  in userlist.userses" :key="indexinneruser">
                                                <div class="form-check form-check-inline">
                                                                <div class="custom-control custom-checkbox">
                                                                    <input type="checkbox"
                                                                        :class="{ 'chkItems custom-control-input': true }"
                                                                        
                                                                        :id="'chk_' + inneruser.id" v-bind:value="inneruser.id"  v-model="checkBoxUser" @change="updateCheckall($event,userlist)" style="display: none;">
                                                                    <label
                                                                        class="custom-control-label universal-custom-control-label pt-1"
                                                                        :for="'chk_' + inneruser.id"> {{ inneruser.name }} </label>
                                                                </div>

                                        </div>

                                            
                                            </li>
                                        </ul>
                                    </div>
                                </div> 
                            </template>
                        </dynamic-form>
                        
                        <div class="py-2">
                            <span class="mandatory">{{$t('MandatoryString')}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div> 
    
</div>   
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default{
    props:{
        ProjectDetails:{
            type:Number
        },
        TaskId:{
            type:Number
        }
    },
    data(){
        return{
            isLoading: false,
            GetUserList:[],
            checkBoxUser:[],
            checkBoxUserType:false,
            selectedUserTypeId:0,
            fileName:'',
            attachement_mime:'',
            base64String:'',
            fileData:[],
            modelUserId:'',
            modelInnerUserId:'',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FileTitle'),
                            name: "FileTitle",
                            value: "",
                            placeholder: "Enter File Title",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true,
                            },
                        },
                        //{
                           // astype: "SlotField",
                           // label: this.$t('File'),
                           // name: "AttachmentFile",
                           // value: "",
                           // placeholder: "",
                           // config: {
                               // options: [],
                               // showAddIcon: false,
                               // onChange: this.onChangeDropdown
                            //},
//validationRules: "required",
                        //},
                        {
                            astype: "FileField",
                            label: this.$t('File'),
                            showLabel: true,
                            name: "Attachment",
                            placeholder: "",
                            validationRules: "required",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'doc', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'docx', // Document type
                                        iconClass: 'fa-file-word-o' // Icon class for documents
                                    },
                                    {
                                        fileType: 'txt', // Text file type
                                        iconClass: 'fa-file-text-o' // Icon class for text files
                                    },
                                    {
                                        fileType: 'jpg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'jpeg', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'png', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'gif', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'bmp', // Image types
                                        iconClass: 'fa-file-image-o' // Icon class for images
                                    },
                                    {
                                        fileType: 'mp4', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    {
                                        fileType: 'mov', // Video types
                                        iconClass: 'fa-file-video-o' // Icon class for videos
                                    },
                                    // Add more image, document, text, and video types as needed
                                ],
                                onChange: this.previewFiles,
                                showAddIcon: false,
                                onAddButtonClick: "",
                                multiple: false
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data:[
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    group_name: 'Visible To',
                    group_display_name: this.$t('Visibleto'),
                    Data:[
                        {
                            astype: "SlotField",
                            label: "",
                            name: "CheckBoxMultiple",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        }
                    ]
                }
            ],
            buttons:[
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-upload pr-2'></i>Upload"
                },
                {
                    type: "button",
                    class: "btn btn-primary delete-btn mr-2 my-sm-0",
                    text: "<i class='fa fa-times pr-2'></i>Cancel",
                    onClick: this.closePopup    
                }
            ],
             model : {
                FileId:0,
                FileName: '',
                FilePath: '',
                FileTitle: '',
                FileContent:'',
                UserList: [],
                FileVersion:0,
                ProjectId:0,
                TaskId:0,
                FileDetail:'',
                StatusId:0,
                FileCategoryId:0,
                RequestType:'first',
                formData:{}

            }

        }
    },
    created(){
        var vm = this;
        vm.GetChecboxUserData(vm.ProjectDetails);
    },
    methods:{
        closePopup(){
            this.$parent.ShowManageFile = false;
        },
       
        async GetChecboxUserData(id){
            //debugger;
            
            var vm = this;
            var params = `projectId=${id}`;
            await DataService.GetChecboxUserData(params).then(response=>{
                if(response.data.result.length > 0){
                    vm.GetUserList = response.data.result;
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        previewFiles(event) {
            var vm = this;
            vm.fileData.push(event.target.files[0]);
        },
        // GetBase64String: function (evt) {
        //     // debugger;
        //         //var file = input.files[0];
                
        //         var vm = this;
        //         vm.fileData = [];
        //         var f = evt.target.files[0];
        //         vm.fileData.push(evt.target.files[0]);
        //         // var reader = new FileReader();
             
        //         // reader.onload = (function (theFile) {
        //         //     return function (e) {
        //         //         var binaryData = e.target.result;
        //         //         var base64StringEncoded = window.btoa(binaryData);
        //         //         var fileName = f.name;
        //         //         var n = fileName.lastIndexOf(".");
        //         //         var extensionName = fileName.substring(n + 1, fileName.length);
        //         //         var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        
        //         //         fileName = f.name;
        //         //       var  fileNewObj = {
        //         //             attachement_mime: attachemnetMime,
        //         //             fileName: fileName,
                            
        //         //             base64String: base64StringEncoded,
        //         //         };
        //         //         vm.fileData.push(fileNewObj);
                        
        //         //     };
        //         // })(f);
             
        //         // reader.readAsBinaryString(f);
              
        //     },

        async onSubmit(){
            //debugger;
            var vm = this;
            vm.isLoading = true;
            var getUserCheckedValue = ''
            vm.checkBoxUser.map((item,index)=>{
                getUserCheckedValue += index ==0 ? item : ','+item
            })
            if(vm.fileData=="")
            {
                vm.ShowAlert(vm.$t('SelectFileUpload'), "warning", true, vm.$t('Alert'));
                vm.isLoading = false;
                return false ;
            }

            if(vm.checkBoxUser=="" && vm.fileData !="")
            {
                vm.ShowAlert(vm.$t('SelectUserToShareFile'), "warning", true, vm.$t('Alert'));
                vm.isLoading = false;
                return false ;
            }
            else{
            vm.model.FileId=0,
            vm.model.FileName= vm.getFileName,
            vm.model.FileTitle= vm.FormSchema[0].Data[0].value,
            vm.model.FileContent=vm.FormSchema[1].Data[0].value,
            vm.model.UserList= getUserCheckedValue,
            vm.model.ProjectId=vm.ProjectDetails,
            vm.model.TaskId=vm.TaskId
            vm.model.RequestType="Post",
            vm.model.requestTypeValue='first'
            vm.model.ModuleName='Project'
            var formdata = new FormData();
            formdata.append('file',vm.fileData[0]);
            vm.model.formData=vm.fileData[0]
            await DataService.Create(vm.model).then(res=>{
                if(res.data.result.msg=="FileAddedMsg"){
                this.$emit('close')
                this.$emit('closeUpdateModal')
                this.$emit('fetchData')
                this.ShowAlert(this.$t('FileAddedMsg'), "success", true, this.$t('Alert'));
                }
                if(res.data.result.msg=="ProcessingErroeOccured"){
                    this.$emit('close')
                    this.$emit('closeUpdateModal')
                    this.$emit('fetchData')
                this.ShowAlert(this.$t('ProcessingErroeOccured'), "danger", true, this.$t('Alert'));
                }
                if(res.data.result.msg=="ErrorAddFile"){
                    this.$emit('close')
                    this.$emit('closeUpdateModal')
                    this.$emit('fetchData')
                this.ShowAlert(this.$t('ErrorAddFile'), "danger", true, this.$t('Alert'));
                }
                //debugger
            }).catch(err=>{
                console.log(err);
            })
            vm.isLoading = false;
        }
        },
        updateCheckall(e,userlist)
        {
        //debugger;
        var vm  = this;
       if(userlist.userses.length==vm.checkBoxUser.length)
       {
      vm.checkBoxUserType=true;
      vm.selectedUserTypeId=userlist.userTypeId;
        }
       else
       {
         vm.checkBoxUserType=false;
         vm.selectedUserTypeId=userlist.userTypeId;
        //vm.checkBoxAll = [];
       }
          
        },
        CheckUserTypeAll(e,userlist){
           // console.log("selectedUserType",userlist.userTypeId)
            //debugger;
            var vm  = this;
            if(e.target.checked)
            {
                userlist.userses.forEach((select) => {
                this.checkBoxUser.push(select.id)
                 })
            }
            else
            {
              vm.checkBoxUser = [];
            }
                
        },
        
     }
}
</script>
<style>
  #AttachmentFile {
   position:relative;
 }
 #AttachmentFile .input-group .question-circle{
    position:absolute;
    top:-24px;
    left: 34px;
 } 
 .custom-file .bootstrap-filestyle input{   
 padding-bottom: 7px;
    margin-bottom: 3.55px;
}
.custom-file .bootstrap-filestyle .choose-file{
    margin-bottom:4px;
}
</style>