<template>
    <div>
        <div class="my-popups">
            <div class="modal d-block ">

                <div class="modal-dialog ui-draggable" style="display: inline-block;">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle">
                            <h5 id="ui-id-26" class="modal-title">{{ $t('RequestTime') }}</h5><button type="button"
                                title="" @click="$emit('close')" class="close" data-original-title="Close"><i
                                    class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <div id="backgroundPopup" class="modal-body"
                            style="height: auto; min-height: 0px; max-height: none;">
                            <section id="main-content" class="main-content">
                                <section class="main-content">
                                    <div class="container-fluid p-0">
                                        <div class="p-0">

                                            <ValidationObserver v-slot="{ handleSubmit }">
                                                <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                                                    <div class="row">
                                                        <div class="col-md-6">
                                                            <div class="form-group"><label>{{ $t('TrackedTime')
                                                                    }}:</label>
                                                                <input type="text" v-model="time" disabled="disabled"
                                                                    class="form-control">
                                                            </div>
                                                        </div>
                                                        <div class="col-md-6">
                                                            <div class="form-group">
                                                                <ValidationProvider rules="required"
                                                                    v-slot="{ errors }">
                                                                    <label>Title:<span
                                                                            class="text-danger">*</span></label>
                                                                    <div class="input-group mb-3">
                                                                        <input type="text" v-model="requestData.title"
                                                                            placeholder="Title" maxlength="100"
                                                                            name="RequestTitle"
                                                                            :class="errors[0] ? 'form-control is-invalid' : 'form-control'">
                                                                        <span class="invalid-feedback">{{ errors[0] ?
                                $t('ExpTitleRequired') : '' }}</span>
                                                                    </div>
                                                                </ValidationProvider>
                                                            </div>
                                                        </div>
                                                        <div class="col-md-6">
                                                            <div class="form-group">
                                                                <ValidationProvider rules="required" v-slot="{ errors }">

                                                                    <label>{{ $t('Time') }}:({{ $t('InHours') }})<span
                                                                            class="text-danger">*</span></label>
                                                                    <div class="input-group mb-3">
                                                                        <input type="text"  
                                                                            v-model="requestData.timeInHours" v-on:keypress="isNumber($event)"
