                                $t('TimeReq') : '' }}</span>
                                                                </ValidationProvider>
                                                            </div>
                                                        </div>
                                                        <div class="col-md-6">
                                                            <div class="form-group"><label>{{ $t('Select') }}:</label>
                                                                <div class="form-group pl-0 border-0">
                                                                    <div class="form-check form-check-inline">
                                                                        <div
