                                                                                id="rdo_0" value="1"
                                                                                class="form-check-input custom-control-input">
                                                                            <label class="custom-control-label"
                                                                                for="rdo_0"></label>
                                                                        </div>
                                                                        <label for="UserType"
                                                                            class="form-check-label font-weight-normal">
                                                                            {{ $t('CLFULL') }}
                                                                        </label>
                                                                    </div>
                                                                    <div class="form-check form-check-inline">
                                                                        <div
                                                                            class="custom-control custom-radio custom-control-inline">
                                                                            <input type="radio" v-model="clientOrPm"
                                                                                @change="clientsDropdownChange"
                                                                                id="rdo_1" value="2"
                                                                                class="form-check-input custom-control-input">
                                                                            <label class="custom-control-label"
                                                                                for="rdo_1"></label>
                                                                        </div>
                                                                        <label for="UserType"
                                                                            class="form-check-label font-weight-normal">
                                                                            {{ $t('PM') }}
                                                                        </label>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="col-md-6">
                                                            <label v-if="clientOrPm == 1">{{ $t('client_id') }}<span
                                                                    class="text-danger">*</span></label>
                                                            <label v-else>{{ $t('SelectPm') }}<span
                                                                    class="text-danger">*</span></label>
                                                            <multiselect ref="selectClient" :multiple="true"
                                                                :close-on-select="false"
                                                                @input="setSelectedValue($event)"
                                                                :placeholder="clientOrPm == 1 ? $t('client_id') : $t('SelectPm')"
                                                                class="form-group p-0" v-model="requestData.clients"
                                                                :options="clientOrPm == 1 ? store.getclientsProject : store.getPmProject"
                                                                :label="clientOrPm == 1 ? 'CLIENTNAME' : 'USER_NAME'"
                                                                track-by="USER_ID" :show-labels="false"
                                                                id="exampleFormControlSelect1">
                                                                <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                                            </multiselect>

                                                        </div>
                                                        <!---->
                                                        <div class="col-md-6">
                                                            <div class="form-group"><label> {{ $t('UploadFile')
                                                                    }}:</label> <a href="javascript:;"
                                                                    data-toggle="popoveruserguide"
                                                                    data-content="<small class='form-text text-muted text-left'>Maximum upload size per file: 2 MB<br/>Disallowed File Types: .exe,.com,.js,.php,.asp,.aspx<br/></small>"
                                                                    data-original-title="" title=""><i
                                                                        class="fa fa-question-circle-o text-popover"
                                                                        style="font-size: 14px;"></i></a>
                                                                <div class="input-group">
                                                                    <div class="custom-file">
                                                                        <input type="file" @change="previewFiles"
                                                                            multiple size="4" name="FileSrc"
                                                                            data-vv-as="Image" tabindex="-1"
                                                                            class="custom-file-inputs form-control"
                                                                            id="filestyle-1"
                                                                            style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                                                        <div class="bootstrap-filestyle input-group">
                                                                            <input type="text"
                                                                                class="form-control disabled"
                                                                                v-model="fileName" disabled="">
                                                                            <div class="input-group-append"
                                                                                style="cursor:pointer;">
                                                                                <span
                                                                                    class="group-span-filestyle input-group-btn input-group-text bg-white"
                                                                                    tabindex="0">
                                                                                    <label
                                                                                        title="Click here to select a file."
                                                                                        for="filestyle-1"
                                                                                        class="mb-0 font-weight-normal ">{{
                                $t('FileControlText') }}</label>
                                                                                </span><span
                                                                                    class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                                                    <a href="javascript:;"
                                                                                        class="round-icon-small btn-danger-light clsattclear"
                                                                                        title="Clear"><i
                                                                                            class="fa fa-undo"></i></a>
                                                                                    <a href="javascript:;"
                                                                                        class="round-icon-small btn-danger-light clsattremove d-none"
                                                                                        title="Remove"><i
                                                                                            class="fa fa-times"></i></a>
                                                                                </span>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="col-md-12">
                                                            <div class="form-group">
                                                                <ValidationProvider rules="required" v-slot="{ errors }"
                                                                    class="tt_textarea_span position-relative"
                                                                    name="comment">
                                                                    <label for="FormControlTextarea1">Description:<span
                                                                            class="text-danger">*</span></label>
                                                                    <textarea :placeholder="$t('WriteAComment')"
                                                                        v-model="requestData.comment" name="comment"
                                                                        class="form-control"
                                                                        :class="errors[0] ? 'is-invalid' : ''"
                                                                        style="border-color: #BDBCBD; resize:none"
                                                                        maxlength="2000" />
                                                                    <span class="invalid-feedback">{{ errors[0]
                                                                        }}</span>
                                                                </ValidationProvider>
                                                                <!---->
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div class="row flex-row-reverse">

                                                        <div class="text-end text-right col-12">
                                                            <button type="submit" class="btn btn-success"><i
                                                                    class="fa fa-save pr-2"></i>{{ $t('Save')
                                                                }}</button>
                                                            <button @click="$emit('close')" type="button"
                                                                class="btn btn-danger" style="margin-left: 5px;"><i
                                                                    class="fa fa-close pr-2"></i>{{ $t('Cancel')
                                                                }}</button>
                                                        </div>

                                                    </div>

                                                </form>
                                            </ValidationObserver>
                                        </div>
                                    </div>
                                </section>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message"
            v-bind:open="store.getAlertMessage.open" v-on:close="store.$state.getMessageAlert.open = false" />
    </div>
</template>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import {
    extend,
    ValidationObserver,
    ValidationProvider
} from 'vee-validate';

import Alert from '../Common/Alert.vue';
import VueTimepicker from 'vue2-timepicker'

import DataService from '../../services/DataService';
import {
    getSecondsFromDate,
    formatter
} from "@/assets/common/vue-common.js"
import moment from "moment"
extend('required', {
    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    message: 'The {_field_} is required'

    ///computesRequired: true
});
extend('number', value => {
    if (value >= 0) {
        return true;
    }
});
export default defineComponent({
    name: "Request Time",
    components: {
        ValidationObserver,
        Alert,
        VueTimepicker,
        ValidationProvider
    },
    props: {
        manualData: {
            type: Object
        },
        store: {
            type: Object
        }
    },
    data() {
        return {
            startTime: '00:00',
            endTime: '00:00',
            date: moment().format("YYYY-MM-DD"),
            time: '00:00:00',
            clientOrPm: 1,
            requestData: {
                title: '',
                comment: '',
                msgboardid: '',
                projectid: '',
                taskid: '',
                request_time_sec: '',
                file: '',
                clients: '',
                timeInHours: ''
            },
            messages: {
                MessageBoardContent: '',
                MessageBoardTitle: '',
                RefSourceName: 'RequestTime',
                MessageType: 's',
                TaskId: '',
                ProjectId: '',
                UserList: [],
                MessageBoardId: 0,
                type: 'post',
                formData: {}

            },
            token: '',
            AttachmentFileDetail: [],
            uploadUrl: process.env.VUE_APP_DOCSTORAGE_API + '/UploadStreamNew',
            fileName: ''
        }

    },
    created: function () {
        this.requestData.projectid = this.manualData.projectId
        this.requestData.taskid = this.manualData.taskId
        this.requestData.msgboardid = this.manualData.MESSAGE_BOARD_ID
        this.time = formatter(this.manualData.totalHours)
        this.store.clientsGetFromProjectId(this.manualData.projectId)
        this.store.pmGetFromProjectId(this.manualData.projectId)
        this.clientsDropdownChange()
        this.token = this.GetUserInfo ? this.GetUserInfo.Token : ''
        this.messages.MessageBoardId = this.manualData.MESSAGE_BOARD_ID
        this.messages.TaskId = this.manualData.taskId
        this.messages.ProjectId = this.manualData.projectId

    },
    methods: {
        async clientsDropdownChange() {
            console.log("check", this.manualData.projectid)
            this.requestData.clients = []

        },
        setSelectedValue: function (event) {

            var vm = this;

            var data = [];
            vm.UserList = [];
            if (event.length > 0) {
                $.each(event, $.proxy(function (index, clientteam) {
                    data.push(clientteam.Id);
                }));
                vm.UserList = data.join(',');

            }

        },
        previewFiles(event) {
            var vm = this;
            vm.fileName = event.target.files[0].name;
            vm.AttachmentFileDetail.push(event.target.files[0]);
        },
        async submit() {
            this.requestData.request_time_sec = this.requestData.timeInHours * 60 * 60
            this.messages.MessageBoardTitle = this.requestData.title
            this.messages.MessageBoardContent = this.requestData.comment
            var formData = new FormData();
            formData.append("fileId", 0);
            formData.append("folderId", 0);
            formData.append("moduleName", 'TodoList');
            formData.append("subModuleName", '');
            formData.append("isGlobal", 0);
            formData.append("file", this.AttachmentFileDetail[0]);
            this.messages.formData = this.AttachmentFileDetail[0]
            console.log("checkkk", this.messages)
            await DataService.Post(this.messages).then(async response => {
                if (response.data.code == '0') {
                    this.requestData.msgboardid = response.data.msg
                    this.requestData.projectid = this.manualData.projectId
                    this.requestData.taskid = this.manualData.taskId
                    this.requestData.request_time_sec = this.requestData.timeInHours * 60 * 60
                    await DataService.RequestTime(this.requestData).then(async response => {
                        if (response.data.result != null && response.data.result != "") {
                            this.$emit('close')
                            this.$emit('closeUpdateModal')
                            this.ShowAlert(this.$t('RequestTimeSubmited'), "success", true, this.$t('Alert'));
                        }
                        else {
                            this.ShowAlert(this.$t('Something_Wrong'), "danger", true, this.$t('Alert'));
                        }
                    })

                }

            })

        },
        isNumber: function(evt){
                evt = (evt) ? evt : window.event;
                var charCode = (evt.which) ? evt.which : evt.keyCode;
                if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                    evt.preventDefault();
                } else{return true;}
            },
        // SendAttactment: async function () {
        //     debugger
        //     var vm = this;
        //     var formData = new FormData();
        //     formData.append("fileId", 0);
        //     formData.append("folderId", 0);
        //     formData.append("moduleName", 'Communication');
        //     formData.append("subModuleName", '');
        //     formData.append("isGlobal", 0);
        //     formData.append("file", vm.AttachmentFileDetail[0]);
        //     await DataService.SendAttactment(formData).then(response => {
        //         debugger
        //         //vm.progressValue+=55;
        //         if (response.data.errorMsg == null) {
        //             console.log("no error ")
        //         }
        //         else {
        //             vm.fileInProgress = false;
        //             vm.ShowAlert(response.data.errorMsg, "failure", vm.$t('Alert'));
        //             vm.AttachmentFileDetail = []
        //         }
        //         vm.AttachmentFileDetail = []
        //     })
        //         .catch((err) => {
        //             console.log(" error ")
        //             console.error(err);
        //             vm.isLoading = false;
        //         });

        // },


    },

});
</script>
