<template>
    <section>
        <div class="my-popups">
            <!-- The Modal -->
            <div class="modal my-popups d-block" id="myModal2">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <!-- Modal Header -->
                        <div class="modal-header">
                            <h4 class="modal-title">Update Progress</h4>
                            <span class="user-guide mt-2 mr-2 float-right">
                                <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button"
                                    aria-expanded="false" aria-controls="collapseExample">User Guide</a>
                                <div id="collapseReply" class="divancuserguide collapse">
                                    <div id="scrollbarreplyuserguide" class="custom-scrollbar-js"
                                        style="overflow-x: hidden;">
                                        <div class="con">
                                            <!-- @Html.Raw(Resources.UgProjectMngtToDoUpdate) -->
                                        </div>
                                    </div>
                                </div>
                            </span>
                            <button type="button" v-on:click="$emit('close')" class="close" data-dismiss="modal"><i
                                    class="fa fa-times" aria-hidden="true"></i></button>
                        </div>
                        <!-- Modal body -->

                        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
                            <form v-on:submit.prevent="handleSubmit(UpdateProgress)" ref="form">
                                <div class="modal-body">
                                    <div class="col-md-12 p-0">
                                        <div class="row">
                                            <div class="col-md-12">
                                                <div class="form-group">
                                                    <label>{{ $t('TrackedTime') }} :</label>
                                                    <input type="text" v-model="TRACKED_HOURS" class="form-control"
                                                        placeholder="00:00:00" disabled>
                                                </div>
                                            </div>
                                            <div class="col-sm-12">
                                                <label> {{ $t('SetPercentage') }} :</label>
                                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                    <div class="slider_setpercentage float-left mt-2">

                                                        <VueSlider ref="slider" v-model="PROGRESS"
                                                            v-bind="slideroptions" />
                                                    </div>
                                                    <input type="text" v-model="PROGRESS"
                                                        class="form-control float-right mb-0 slider_setpercentagebox"
                                                        readonly="">
                                                </div>
                                            </div>
                                            <div class="col-md-12">
                                                <div class="form-group">
                                                    <label>{{ $t('Comments') }} :<span
                                                            class="mandatory">*</span></label>
                                                    <ValidationProvider rules="required" v-slot="{ errors }"
                                                        class="tt_textarea_span position-relative" name="Comment">
                                                        <textarea class="form-control" v-validate="'required'"
                                                            :class="errors[0] ? 'form-control is-invalid' : 'form-control'"
                                                            placeholder="write a comment..." name="ProgressComment"
                                                            v-model="ProgressComment"></textarea>
                                                        <span class="invalid-feedback">{{ errors[0] ? $t('CommentIsReq')
                                                            :
                                                            '' }}</span>
                                                    </ValidationProvider>
                                                    <!-- <span v-show="" class="invalid-feedback"></span> -->
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                                <div class="modal-footer">
                                    <button class="btn btn-success save-btn" type="submit"><i
                                            class="fa fa-save pr-2"></i><span>{{ $t('Submit') }}</span></button>
                                    <a href="javascript:;" class="btn btn-danger delete-btn"
                                        v-on:click="ProgressCancelBtn()"><i class="fa fa-close pr-2"></i><span>{{
                                            $t('Cancel') }}</span></a>
                                </div>
                            </form>
                        </ValidationObserver>
                        <div class="col-12">
                            <div class="row">
                                <div class=" listing border" id="divconversationbox"></div>
                                <div class=" col-12 tab-pane fade active show">
                                    <div v-if="CommentList.length == 0"
                                        class="row ticket-card-container card-view bg-border p-3">
                                        <div class="col-sm-12 col-md-12 col-lg-12 mt-2 text-center">
                                            <span class="text-danger ">NoComments</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-12 listing mt-4" v-if="CommentList.length != 0">

                                    <div class="listing tab-content">
                                        <div class="col-12 d-flex border border-bottom-0 bg-f5f5f5">
                                            <div class="col-md-6 pl-0">
                                                <h6 class="py-3 mb-0">
                                                    Comment
                                                </h6>
                                            </div>
                                            <div class="col-md-6 text-right pr-0 bottom_filter_button">
                                                <a href="javascript:;" id="ancreplyExpandAllCollapseAll"
                                                    class="position-relative" style="z-index:999;" title=""
                                                    data-toggle-tooltip="tooltip"
                                                    data-original-title="Expand All or Collapse All"
                                                    @click="expendOrcollapse">Expand All or Collapse All</a>
                                            </div>
                                        </div>



                                        <div class="tab-pane fade show activity-tab active pt-2"
                                            style="overflow-y:auto;max-height:500px;"
                                            v-for="(items, index) in CommentList" :key="index">
                                            <div id="accordion" class="w-100">
                                                <div class="card">
                                                    <div class="card-header">
                                                        <a class="card-link" data-toggle="collapse"
                                                            href="#collapseOne1requestcomment1"
                                                            @click="toggleCollapse(index)" aria-expanded="true">
                                                            <div class="conversation-date heading-border"><span><i
                                                                        class="fa fa-angle-down rotate-icon pr-1"
                                                                        aria-hidden="true"></i>{{
                                                                            dateFormat(items.dateGroup) }}</span></div>
                                                        </a>
                                                    </div>
                                                    <div id="collapseOne1requestcomment1" v-if="isCollapsed(index)"
                                                        class="collapse show" style="display: block;">
                                                        <div class="card-body" v-for="item, k in items.taskCommentList"
                                                            :key="k">

                                                            <div class="conversation-main">
                                                                <div class="conversation-time">
                                                                    <span>{{ timeFormat(item.createdAt) }} {{ }}</span>
                                                                </div>
                                                                <div class="conversation-box-con">
                                                                    <div class="conversation-box">
                                                                        <div class="name-circle light-yellow">

                                                                            <img v-if="item.avatar == 'profile-img.jpg' && item.avatar == null"
                                                                                class="img-circle avatar" src=""
                                                                                width="51" height="51" alt="" />

                                                                            <img v-else class="img-circle avatar"
                                                                                :src="item.avatar" width="51"
                                                                                height="51" alt="" />


                                                                        </div>
                                                                        <div class="conversation-content">

                                                                            <div
                                                                                class="conversation-header addednote-ticket">
                                                                                <span>
                                                                                    <span class="sender-name">{{
                                                                                        item.userName }}</span>

                                                                                </span>
                                                                            </div>
                                                                            <div class="conversation-content">
                                                                                <div class="col-12 p-0 notesdata">
                                                                                    <h6 class="text-muted"><i> Progress:
                                                                                            {{ item.progress }} % </i>
                                                                                    </h6>
                                                                                </div>
                                                                            </div>
                                                                            <div
                                                                                class="conversation-header addednote-ticket">
                                                                                <span>

                                                                                    <span class="sender-department">
                                                                                        {{ item.comment }}</span>
                                                                                </span>
                                                                            </div>

                                                                        </div>


                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>
                                    </div>

                                </div>

                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        <loader :isVisible="store.getLoadingStatus"></loader>
    </section>
</template>

<script>
import DataService from '../../services/DataService'
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'
import {
    getSecondsFromDate,
    getPreviousDay
} from "@/assets/common/vue-common.js"
import moment from "moment"
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
// import i18n from '@/localization/index'
extend('required', {

    validate(value) {

        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    //message: 'The {_field_} is required'

    ///computesRequired: true
});
export default {
    name: "Update Comment",
    components: {
        VueSlider,
        ValidationProvider,
        ValidationObserver
    },
    props: {
        store: {
            type: Object

        }
    },
    data() {
        return {
            TRACKED_HOURS: '',
            TASK_ID: '',
            PROGRESS: '',
            ProgressComment: '',
            value: 0,
            slideroptions: {
                dotSize: 14,
                width: 'auto',
                height: 4,
                contained: false,
                direction: 'ltr',
                data: null,
                min: 0,
                max: 100,
                interval: 1,
                disabled: false,
                clickable: true,
                duration: 0.5,
                adsorb: false,
                lazy: false,
                tooltip: 'focus',
                tooltipPlacement: 'top',
                tooltipFormatter: void 0,
                useKeyboard: false,
                enableCross: true,
                fixed: false,
                minRange: void 0,
                maxRange: void 0,
                order: true,
                marks: false,
                dotOptions: void 0,
                process: true,
                dotStyle: void 0,
                railStyle: void 0,
                processStyle: void 0,
                tooltipStyle: void 0,
                stepStyle: void 0,
                stepActiveStyle: void 0,
                labelStyle: void 0,
                labelActiveStyle: void 0,

            },
            CommentList: [],
            collapsedSections: [false],
        }
    },
    created: function () {
        this.TRACKED_HOURS = this.$parent.TRACKED_HOURS;
        this.TASK_ID = this.$parent.TASK_ID;
        this.PROGRESS = this.$parent.PROGRESS;
        this.GetTaskComment(this.TASK_ID);
        this.toggleCollapse(0)

    },
    methods: {
        ProgressCancelBtn: function () {
            var vm = this;
            vm.StopTimerBtn = false;
            vm.UpdateProgress = false;
            vm.$emit('close');
        },
        UpdateProgress: function () {
            var vm = this;
            ;
            var obj = {
                LogID: null,
                TaskID: vm.TASK_ID,
                Progress: vm.PROGRESS,
                Comment: vm.ProgressComment,
                StatusId: 1001,
            };
            var objectPostString = JSON.stringify(obj);
            this.store.$state.loading = true
            DataService.UpdateProgress(objectPostString).then(response => {

                if (response.data != null && response.data != "") {
                    console.log("checkk", response.data)

                    if (response.data.Status == "Success") {
                        vm.ProgressComment = '';
                        vm.StopTimerBtn = false;
                        // vm.ShowAlert(i18n.t('ProgressSuccessfully'), "success", true, i18n.t('Alert'));
                        vm.$emit('close');
                        const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                        this.store.getTimerListing1(url)
                        const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                        this.store.getTrackedtimings(url1)

                    } else {
                        // vm.ShowAlert(i18n.t("Something_Wrong"), 'failure', true, i18n.t('Alert'));

                    }
                }
                this.store.$state.loading = false

            });

        },
        GetTaskComment: function (taskid) {
            var params = `taskId=${taskid}`
            this.store.$state.loading = true
            DataService.TaskLogCommentDetail(params).then(response => {

                this.CommentList = response.data;
                console.log("commentList", response);
                this.store.$state.loading = false
            });

        },
        isCollapsed(index) {
            // Check if the section at the given index is collapsed
            return this.collapsedSections[index];
        },
        toggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.collapsedSections, index, !this.collapsedSections[index]);
        },
        dateFormat(value) {
            return getPreviousDay(value)
        },
        timeFormat(value) {
            return moment(value).format('HH:mm')
        },
        expendOrcollapse() {
            let collapsOn = true
            this.collapsedSections.map((item) => {
                if (item) {
                    collapsOn = false
                }
            })
            if (collapsOn) {
                this.collapsedSections = []
                this.CommentList.map((items, index) => {
                    this.toggleCollapse(index)
                })
            } else {
                this.collapsedSections = []
            }

        }

    }

}
</script>
