                  <label>{{ $t("Dashboard Name") }}</label><span class="required">*</span>
                  <input v-bind:class="{ 'form-control': true }" id="dashboradName" name="dashboradName" maxlength="200"
                    type="text" v-model="dashboradName" @input="checkSpaces" />

                  <span v-if="hasOnlySpaces && dashboradName.length > 0" class="error-message">Input cannot contain only
                    spaces</span>
                  <span v-if="dashboardRequired && dashboradName.length == 0" class="error-message">Dashboard Name is
                    required</span>
                </div>
              </div>
            </div>
            <!-- <div class="d-flex align-items-center mb-3">
               <label for="enableTodoList">{{ $t("EnableToDoList") }}:</label>
               <div
                  id="enableTodoList"
                  class="custom-switch ml-2"
                  :class="{ 'switch-on': addToDoList, 'switch-off': !addToDoList }"
                  @click="toggleSwitch"
               >
                  <div class="slider"></div>
               </div>
               <div class="ml-2" :class="{ 'switch-on-color': addToDoList, 'switch-off-color': !addToDoList }">
                  {{ addToDoList ? "Added To-Do-List" : "Add To-Do-List" }}
               </div>
            </div> -->

            <!-- System Report -->
            <div id="accordionEx" class="accordion md-accordion left-filter">
              <div class="border bg-light">

                <!-- System Report start -->
                <!-- <div class="card mb-2 p-2">
                  <div role="tab" id="MainColumn0" class="card-header heading-border p-0">
                    <h5 class="mb-0">
                      <a data-toggle="collapse" data-parent="#accordionEx" data-target="#ColumnCollapse0"
                        href="javascript:;" aria-expanded="true" aria-controls="ColumnCollapse0" class="d-block">
                        <span class="Textstyle"> System Reports</span><em aria-hidden="true"
                          class="fa fa-angle-down rotate-icon"></em>
                      </a>
                    </h5>
                  </div>
                  <div id="ColumnCollapse0" role="tabpanel" aria-labelledby="MainColumn0" data-parent="#accordionEx"
                    class="multi-collapse collapse show" style="">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 mt-1">
                     
                      <div class="card" v-for="(option, index) in SystemReport" :key="option.privilege_id">
                        <div class="card-header">
                          
                          <div :id="'systemId' + index" class="d-flex justify-content-between align-items-center">
                            <div class="d-flex align-items-center">
                              <input class="chkItems" :data-reportid="option.privilege_id"
                                :data-chartType="option.chart_type == null || option.chart_type == undefined ? 'table' : option.chart_type"
                                type="checkbox" />
                              <div class="ml-2">{{ $t(option.privilege_name) }}</div>
                            </div>
                            <span v-if="option.privilege_type === 'WIDGET_MAX'"
                              style="font-weight: normal; padding: 5px; line-height: 10px;"
                              class="badge badge-danger float-right">Graph Widget</span>
                            <span v-else style="font-weight: normal; padding: 5px; line-height: 10px;"
                              class="badge badge-success float-right">Numeric Widget</span>
                          </div>
                          
                        </div>
                       
                      </div>
                      
                    </div>
                    
                  </div>
                </div> -->
                <!-- System Report end -->

                <!-- Users Report start -->
                <div class="col-12 p-3">
                  <div role="tab" id="MainColumn1" class="card-header heading-border p-0">
                    <h5 class="mb-0">
                      <a data-toggle="collapse" data-parent="#accordionEx" data-target="#ColumnCollapse1"
                        href="javascript:;" aria-expanded="true" aria-controls="ColumnCollapse1" class="d-block">
                        <span class="Textstyle p-0">Reports</span><em aria-hidden="true"
                          class="fa fa-angle-down rotate-icon"></em>
                      </a>
                    </h5>
                  </div>
                  
                  <div id="ColumnCollapse1" role="tabpanel" aria-labelledby="MainColumn1" data-parent="#accordionEx"
                    class="multi-collapse collapse show" style="">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 mt-4">
                      <div id="accordion" role="tablist" class="accordion md-accordion w-100" v-if="reportGroups">
                        <div class="cardtopbod bg-light">
                          <div class="card " v-for="(reportGroup, index) in reportGroups" v-bind:key="index">
                            <div class="card-header">
                              <a class="card-link collapsed" data-toggle="collapse" data-parent="#accordion"
                                :href="'#collapseOne' + index" aria-expanded="true" :data-target="'#collapseOne' + index"
                                :aria-controls="'collapseOne' + index">
                                <div class="">
                                  <span><em class="fa fa-folder pr-1" aria-hidden="true"></em>{{
                                    reportGroup.reports[0].folderName }}</span>
                                </div>
                              </a>
                            </div>
                            <div :id="'collapseOne' + index" class="collapse" role="tabpanel" data-parent="#accordion"
                              style="">
                              <div class="card-body">
                                <div class="row" >
                                  <div class="col-md-12 listing">
                                    <div class="table-responsive table-fix-header dynamic-dashboard-add-report-table">
                                      <table :id="'tblReport' + index" class="table table-bordered mt-0">
                                        <thead class="thead-bg">
                                          <tr>
                                            <th class="text-center">
                                              <div class="custom-control custom-checkbox">
                                              <input data-v-765974b3="" class="chkItems1 custom-control-input" v-on:change="IsSelectAllCheckBox($event, reportGroup)"
                                              :checked="reportGroup.isAllCheckBoxSelected" type="checkbox" style="display: none;" />
                                              <label class="custom-control-label universal-custom-control-label pt-1" for="chkAll_20"></label>
                                            </div>
                                            </th>
                                            <th>Report Name</th>
                                            <th v-if="moduleCode == 'Home'">Module Name</th>
                                            <th>Type</th>
                                            <th>View Type</th>
