                                                type="checkbox" />
                                            </td>
                                            <td>{{ report.reportName }}</td>
                                            <td v-if="moduleCode == 'Home'">{{report.moduleName}}</td>
                                            <td>
                                              <span v-if="(report.chartType === 'Pie') || (report.chartType === 'Bar') || (report.chartType === 'Donut')|| (report.chartType === 'Line') || (report.chartType === 'Horizontal_Bar') || (report.chartType === 'Multiple_Bar')"
                                              style="font-weight: normal; padding: 5px; line-height: 10px;"
                                              class="badge badge-danger float-right">Graph Widget</span>

                                              <span v-else-if="report.chartType === 'Numbers_Chart'"
                                              style="font-weight: normal; padding: 5px; line-height: 10px;"
                                              class="badge badge-success float-right">Numeric Widget</span>

                                              <span v-else style="font-weight: normal; padding: 5px; line-height: 10px; color: white;"
                                              class="badge badge-warning float-right">Table Widget</span>
                                            </td>
                                            <td>
                                              
                                              <label :class="{'newswitch_toggle switch auto':true,'disabled':report.chartType == 'Numbers_Chart'}"
                                                v-if="report.chartType != 'Table'">
                                                <input :id="'chartType' + reportIndex + '_' + index"
                                                  class="form-check-input rdoticketfor custom-control-input dynamic"
                                                  type="checkbox" v-bind:name="'viewtype'"
                                                  v-bind:checked="report.chartType"
                                                  v-bind:value="report.chartType ? report.chartType : 'Table'" />
                                                <span class="slider round">
                                                  <span class="slider-yes">Graph</span>
                                                  <span class="slider-no">Table</span>
                                                </span>
                                              </label>
                                            </td>
                                          </tr>
                                        </tbody>
                                      </table>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <!-- Users Report end -->

              </div>
            </div>
          </div>
          <div class="float-right my-3">
            <a href="javascript:;" data-tour="step: 1; title: enter DealDetail; content: AddDealDetailContent"
              class="btn btn-success float-right" title="Save" v-on:click="saveDashboard()"><em
                class="fa fa-save pr-2"></em>Save
            </a>
          </div>
        </div>
      </div>
    </div>
    <loader :isVisible="isLoading"></loader>
  </div>
</template>
<style scoped>
.required {
  color: red;
  margin-left: 3px;
  /* Adjust spacing as needed */
}
.dynamic-dashboard-add-report-table input:checked + .slider:before{
   left: 44px !important;
}
.error-message {
  color: red;
  font-size: 12px;
  font-weight: 500;
}
</style>
<script>
import DataService from "../../services/DataService";
export default {
  props: {
    moduleCode: null,
    dashboardid: null,
  },
  data: function () {
    return {
      isLoading: false,
      reportGroups: [],
      SystemReport: [],
      dashboradName: "",
      newDashboardId: null,
      hasOnlySpaces: false,
      dashboardRequired: false,
      addToDoList: false,
      IsAllCheckBoxChecked: false,
    };
  },
  created() {
    this.GetDynamicReport();
  },
  mounted() {
    // eslint-disable-next-line no-undef
    $("#modalprojectquickviewpopforadddabhboard").modal("show");
    this.CheckBoxBootstrap();
  },
  methods: {

    IsSelectAllCheckBox: function(e, ths){
     
         if(e.target.checked){
            $(e.target).closest("table").find("tbody tr td:first-child input.custom-control-input").prop("checked", true);
         } else {
            $(e.target).closest("table").find("tbody tr td:first-child input.custom-control-input").prop("checked", false);
         }
         ths.isAllCheckBoxSelected =  e.target.checked;
      },
      IsSelectCheckBox: function(event, itm){
         
        
         var TotalLength = $(event.target).closest("table").find("tbody tr td:first-child input.custom-control-input").length;

         var checkedLength = $(event.target).closest("table").find("tbody tr td:first-child input.custom-control-input:checked").length;
         
         if(TotalLength == checkedLength)
         {
          itm.isAllCheckBoxSelected =  true;
         }
         else{
          itm.isAllCheckBoxSelected =  false;
         }
        
         
      },
    checkSpaces() {
      // if (this.dashboradName.trim().length === 0) {
      //   this.hasOnlySpaces = true;
      //   console.log(this.dashboradName.trim().length === 0);
      // } else {
      //   console.log(this.dashboradName.trim().length === 0);
      //   this.hasOnlySpaces = false;
      // }
      this.hasOnlySpaces = /^\s*$/.test(this.dashboradName);
    },
    toggleSwitch() {
      this.addToDoList = !this.addToDoList;
    },
    CloseDashboardView: function () {
      var vm = this;
      vm.$parent.showAdddashboard = false;
    },
    SelectSystemReports: function (event) {
      const clickedLabel = event.currentTarget;
      clickedLabel.classList.toggle("active");
    },
    GetDynamicReport() {
      
      var vm = this;
      var param = `dashboardId=${vm.dashboardid}&moduleName=${vm.moduleCode}`;
      DataService.GetDynamicReport(param).then(function (response) {
        var data = response.data.UserReport;
        
        data.forEach((x) => { x.isAllCheckBoxSelected = false});
            vm.reportGroups = data;

        setTimeout(() => {
          vm.CheckBoxBootstrap();
        }, 1000);
      });
    },
    filterObjects() {
      var vm = this;
      vm.idsToFilter = vm.idsString.split(",");
      vm.filteredObjects = vm.SystemReportFiltered.filter((obj) =>
        vm.idsToFilter.includes(obj.privilege_id.toString())
      );
      return vm.filteredObjects;
    },
    saveDashboard: function () {
        
      var vm = this;
      var reportArr = [];
      // for (let index = 0; index < vm.SystemReport.length; index++) {
      //   // eslint-disable-next-line no-undef
      //   $("#systemId" + index + " .chkItems:checked")
      //     .not("[id^='chkAll']")
      //     .each(function () {
      //       // eslint-disable-next-line no-undef
      //       if ($(this).attr("data-reportid")) {
      //         reportArr.push({
      //           // eslint-disable-next-line no-undef
      //           reportId: $(this).attr("data-reportid"),
      //           // eslint-disable-next-line no-undef
      //           chartType: $(this).attr("data-chartType"),
      //           displayOrder: index,
      //           xaxis: 0,
      //           yaxis: 0,
      //           is_system_report: 1,
      //         });
      //       }
      //     });
      // }
      for (let index = 0; index < vm.reportGroups.length; index++) {
        // eslint-disable-next-line no-undef
        $("#tblReport" + index + " .chkItems:checked")
          .not("[id^='chkAll']")
          .each(function () {
            // eslint-disable-next-line no-undef
            if ($(this).attr("data-reportid")) {
              reportArr.push({
                // eslint-disable-next-line no-undef
                reportId: $(this).attr("data-reportid"),
                // eslint-disable-next-line no-undef
                chartType: $(this)
                  .parent()
                  .parent()
                  .parent()
                  .find(".rdoticketfor")
                  .is(":checked")
                  ? "Graph"
                  : "Table",
                displayOrder: index,
                xaxis: 0,
                yaxis: 0,
                is_system_report: 0,
              });
            }
          });
      }
      var xCount = 0, yCount = 0;
      for (let index = 0; index < reportArr.length; index++) {
        reportArr[index].xaxis = (index % 4) * 3;
        reportArr[index].yaxis = yCount;
        // xCount = reportArr[index].xaxis;
        // yCount = reportArr[index].yaxis;
      }
      if (
        vm.dashboradName != "" &&
        vm.dashboradName != null &&
        vm.dashboradName != undefined &&
        reportArr.length >= 2 &&
        vm.hasOnlySpaces == false
      ) {
        var data = {
          dashboardName: vm.dashboradName,
          dashboardDesc: "added dashboard",
          moduleName: vm.moduleCode,
          reports: reportArr,
          addToDoList: vm.addToDoList,
        };
        var objectPostString = JSON.stringify(data);
        DataService.SaveDashboard(objectPostString).then(function (response) {
          console.log(response);
          if (response.data.status == "Success" && reportArr.length > 0) {
            vm.newDashboardId = response.data.result;
            console.log(vm.newDashboardId);
            vm.ShowAlert(
              vm.$t("AddedSuccess", [vm.$t("Dashboard")]),
              "success",
              true,
              vm.$t("Alert")
            );
            // eslint-disable-next-line no-undef
            $("#modalprojectquickviewpopforadddabhboard").modal("hide");
            vm.$parent.GetDashboard(vm.newDashboardId);
            vm.SystemReport = [];
            vm.UserReport = [];
            vm.dashboradName = "";
            // vm.addToDoList = null;
          } else {
            vm.ShowAlert(
              vm.$t("SameDashboardExist", [vm.$t("Dashboard")]),
              "warning",
              true,
              vm.$t("Alert")
            );
          }
        });
      } else {
        if (reportArr.length <= 1 && vm.dashboradName != "" && vm.dashboradName != null) {
          vm.ShowAlert(
            vm.$t("ReportSelectionRequired", [vm.$t("Dashboard")]),
            "warning",
            true,
            vm.$t("Alert")
          );
        } else if (vm.hasOnlySpaces == true) {
          vm.ShowAlert(
            vm.$t("DashBoardNameRequired", [vm.$t("Dashboard")]),
            "warning",
            true,
            vm.$t("Alert")
          );
        } else {
          vm.dashboardRequired = true;
        }
      }
    },
  },
};
</script>
<style>
.custom-switch {
  width: 48px;
  height: 25px;
  position: relative;
  background-color: #ccc;
  border-radius: 18px;
  cursor: pointer;
}

.switch-on .slider {
  transform: translateX(20px);
}

.switch-off {
  color: #ccc;
  /* Gray text when switch is OFF */
}

.switch-on {
  background: #67a7e4;
  /* Green text when switch is ON */
}

.switch-off,
.switch-on {
  display: inline-block;
  vertical-align: middle;
}

.switch-on-color {
  color: #67a7e4;
}

.switch-off-color {
  color: #ccc;
}

.Textstyle {
  font-size: 1.25rem;
  line-height: 40px;
  text-transform: capitalize;
}

/* .accordion>.card:not(:last-of-type) {
    border-bottom: 1px !important;
} */
</style>
