<template>
     <div class="onlyd-view onlytodo_kb">
        <div  class="canvan-view-list kvfixhead kvminwidth"> 
                <div v-for="(parentItem,parentIndex) in DataArray" :key="parentIndex" v-bind:class="parentItem.className">
                    <h3 v-if="parentItem.show==1">{{parentItem.count}}<span>{{$t(parentItem.name)}} </span></h3>

                    <div class="w-100"> <!-- class="canvan-view-list kvlistview kvminwidth combo-canvan-view" -->
                        
                            <draggable v-bind:list="parentItem.data" class="list-group" v-bind:options="{group:'clothes'}" :data-status="parentItem.name" v-on:start="onStart" v-on:end="onEnd" v-on:change="ChangeStatusDrag($event,parentItem.StatusId)">
                            <div v-for="(item,index) in parentItem.data" :key="index" v-bind:class="parentItem.className" >
                                <div class="card card-exp">
                            <div class="card-header">
                                <h4>
                                    <span v-bind:title="item.TASK_NAME" class="text-capitalize text-primary">{{item.TASK_NAME|truncate(20, '...')}}</span>
                                    <a class="nostylelink mr-3 float-right" data-toggle-tooltip="tooltip" title="View" href="javascript:;" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item)" v-bind:value="item.PROJECT_ID" v-bind:attr="item.PROJECT_ID"><em class="float-right fa fa-eye" aria-hidden="true"></em></a>
                                </h4>
                        
                                <div class="verticle-action mt-1">
                                    <em class="fa fa-ellipsis-v action_icon_cv" aria-hidden="true" style="width:10px;"></em>
                                    <span class="action-list-box small">
                                        <span class="list-actions fsm-list" id="action-list">
                                            <div class="float-left">
                                                <div class="float-left">
                                                   
                                                    <a data-toggle="modal" v-on:click="ProgressDivOpen(item)" v-if="item.PROJECTTASKPROGRESS=='MANUALLY_BASED'" checkPrivilege="yes" href="javascript:;" data-placement="left" style="cursor:pointer;" title="Update Progress" v-bind:class="(item.STATUS.toLowerCase()=='completed' ?'update actions-onclick disabled_icon':'update actions-onclick')">
                                                        <em class="fa fa-pencil action-icon"></em>
                                                        <span class="text">Update Progress</span>
                                                    </a>
                                                    <a v-if="ModuleNameAll!='HR' && ModuleNameAll !='Ticketing'" title="Request Time" data-placement="left" class="update actions-onclick" v-on:click="RequestTimeBtn(item)" v-bind:class="(item.IS_APPROVE_REJECT > 0 ||(['ACT','REFPEN','PNPGRS'].indexOf(item.PROJECT_STATUS_ID)==-1 && item.LINKED_SOURCE_NAME==null)?'edit-list disabled_icon':'edit-list')" href="javascript:;">
                                                        <em class="fa fa-reply text-dark"></em>
                                                        <span class="text">Request Time</span>
                                                    </a>
                                                    <a title="QuickView" data-placement="left" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item)" v-bind:value="item.PROJECT_ID" class="update actions-onclick" href="javascript:;" v-bind:attr="item.PROJECT_ID">
                                                        <em class="fa fa-eye text-info action-icon"></em>
                                                        <span class="text">Quick View</span>
                                                    </a>
                                                </div>
                                            </div>
                                            <em class="fa fa-times close close-action_cv cursor-pointer" aria-hidden="true"></em>
                                        </span>
                                    </span>
                                </div>
                            </div>
                            <div class="card-body">
                                <div class="spac-eq text-capitalize text-dark"><b><em class="fa fa-folder-o"></em> <span>{{item.PROJECT_NAME|truncate(20, '...')}}</span></b></div>
                                <div class="spac-eq" v-if="item.TASK_DESCRIPTION != null && item.TASK_DESCRIPTION != ''"> <em class="fa fa-file-text "></em><span v-html="item.TASK_DESCRIPTION"></span></div>
                                <div class="spac-eq" v-else> <em class="fa fa-file-text "></em> -</div>
                                <div class="spac-eq">
                                    <em class="fa fa-calendar "></em> {{item.START_DATE}} - {{item.END_DATE}} {{ModuleNameAll}}
                                    <a v-if="ModuleNameAll !='HR' && ModuleNameAll !='Expenses'" href="javascript:;" class="text-dark float-right" v-on:click="openNoteListPopUp(item,index)"><em aria-hidden="true" class="fa fa-file-text-o actionicon_20" style="margin-right:5px;"></em><span class="noti-circle noti-orange">{{item.NOTES_COUNT == null ? 0 : item.NOTES_COUNT}}</span></a>
                                </div>
                                <div class="spac-eq">
                                    <span><em class="fa fa-clock-o"></em><span v-if="item.TRACKED_HOURS>0" class="blue-time pl-1">{{convertSecondsToHHMMSSNew(item.TRACKED_HOURS)}} of</span><span class="blue-time pl-1">{{item.ALLOCATE_HOURS}} {{$t('Hrsshort')}} </span></span>
                                    <a v-if="ModuleNameAll !='HR' && item.PROJECT_NAME !='Ticketing'" href="javascript:;" class="text-dark float-right" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item,'attachment')" v-bind:attr="item.PROJECT_ID"><em aria-hidden="true" class="fa fa-paperclip" style="font-size:20px;margin-right:5px;"></em><span class="noti-circle noti-blue">{{item.FILE_COUNT == null ? 0 :item.FILE_COUNT}}</span></a>
                                    <a v-else-if="ModuleNameAll !='HR' && item.PROJECT_NAME =='Ticketing'" href="javascript:;" class="text-dark float-right" v-on:click="$('#DivViewDetails').data({'ticketId':item.LINKED_SOURCE_ID,'status':item.STATUS,'number':item.TASK_NAME}).dialog('open')" v-bind:attr="item.PROJECT_ID"><em aria-hidden="true" class="fa fa-paperclip" style="font-size:20px;margin-right:5px;"></em><span class="noti-circle noti-blue">{{item.FILE_COUNT == null ? 0 :item.FILE_COUNT}}</span></a>
                                </div>
                                <div class="spac-eq text-capitalize">
                                    <b><em class="fa fa-user"></em> <span>{{item.NAME}}</span></b>
                                    <a v-if="ModuleNameAll !='HR'" href="javascript:;" class="text-dark float-right" v-on:click="OpenCommentModel(item)"><em aria-hidden="true" class="fa fa-commenting-o" style="font-size:20px;"></em><span class="noti-circle noti-green">{{item.COMMENT_COUNT == null ? 0 : item.COMMENT_COUNT}}</span></a>
                                </div>
                                <div class="spac-eq" v-if="item.TAG_NAME != ''">
                                    <em class="fa fa-tags" aria-hidden="true"></em>
                                    <span class="tags-dashbm" v-for="(tagItem,k) in item.TAG_NAME" :key="k">
                                        {{tagItem.TAG_NAME}}
                                    </span>
                                </div>
                                <div v-show="ModuleNameAll != 'Expenses'" class="spac-eq" v-else><em class="fa fa-tags" aria-hidden="true"></em> - </div>
                                <div class="spac-eq" v-if="item.PRIORITY_NAME != '' && item.PRIORITY_NAME != 'null' && item.PRIORITY_NAME != null">
                                    <span v-bind:class="{'badge':true, 'text-critical-badge': item.PRIORITY_NAME == 'Critical', 'text-medium-badge': item.PRIORITY_NAME == 'Medium', 'text-low-badge': item.PRIORITY_NAME == 'Low'}" id="priority-badge">{{item.PRIORITY_NAME}}</span>
                                </div>
                                <div class="spac-eq border-top text-center">
                                    <div class="time">
                                        <div v-bind:class="'c100 small p'+ parseInt(showTrackPercent(item),10)">
                                            <span>{{showTrackPercent(item)}}%</span>
                                            <div class="slice">
                                                <div v-bind:class="{'bar':showTrackPercent(item) > 0}"></div>
                                                <div class="fill"></div>
                                            </div>
                                        </div>
                                        <span class="t-title">{{ $t('TimeLeft') }} </span>
                                    </div>
                                    <div class="Task">
                                        <div v-bind:class="'cursor-pointer c100 small green p'+ parseInt(item.PROGRESS,10)" v-on:click="ProgressDivOpen(item)" v-if="item.PROJECTTASKPROGRESS=='MANUALLY_BASED'">
                                            <span>{{item.PROGRESS}}%</span>
                                            <div class="slice">
                                                <div v-bind:class="{'bar':item.PROGRESS > 0}"></div>
                                                <div class="fill"></div>
                                            </div>
                                        </div>
                                        <div v-bind:class="'c100 small green p'+ parseInt(item.PROGRESS,10)" v-if="item.PROJECTTASKPROGRESS!='MANUALLY_BASED'">
                                            <span>{{item.PROGRESS}}%</span>
                                            <div class="slice">
                                                <div v-bind:class="{'bar':item.PROGRESS > 0}"></div>
                                                <div class="fill"></div>
                                            </div>
                                        </div>
                                        <span class="t-title">{{ $t('TaskProgress') }}</span>
                                    </div>
                                </div>
                                <div class="spac-eq border-top text-center">
                                    <!-- v-model="item.TotalTime"-->

                                    <div id="TotalTime" v-if="item.AutomaticBtn" class="timerbx green"></div>
                                    <div v-if="item.RUNNING_STATUS=='PAUS' || item.RUNNING_STATUS=='ACT'">
                                        <button v-if="store.getTimer.statusCode=='ACT' && store.getTimer.enableBreak != 'yes'" v-on:click="timerStopOrPause('pause','TimeTrack')" type="button" class="btn Timertodo btn-info">
                                            <span class="tag-name">{{$t('Pause')}}</span>
                                        </button> <button v-if="store.getTimer.statusCode=='PAUS' && store.getTimer.enableBreak != 'yes'" v-on:click="timerStopOrPause('resume','TimeTrack')" type="button" class="btn Timer btn-success">
                                            <span class="tag-name">{{$t('Resume')}}</span>
                                        </button> <button v-if="item.RUNNING_STATUS=='PAUS' || item.RUNNING_STATUS=='ACT'" v-on:click="timerStopOrPause('stop','TimeTrack')" type="button" class="btn Timer btn-danger">
                                            <span class="tag-name">{{$t('Stop')}}</span>
                                        </button>
                                    </div>
                                    <div v-else>
                                        <button :disabled="store.getTimer.trackAgent=='silentapp' ? true : false" type="button" v-bind:class="(item.STATUS.toLowerCase()=='completed'?'btn Timer manually-time inactive-disabled':'btn Timer manually-time')" v-on:click="manualyAddTime(item)" data-toggle="modal" href="javascript:;">
                                            <span class="tag-name"><em class="fa fa-clock-o mr-1"></em>{{ $t('AddManually') }}</span>
                                        </button> <button :disabled="store.getTimer.trackAgent=='silentapp' ? true : false" type="button" v-bind:class="(item.STATUS.toLowerCase()=='completed'?'btn Timer auto-time inactive-disabled':'btn Timer auto-time')" v-on:click="automaticStart(item)" data-toggle="modal" href="javascript:;">
                                            <span class="tag-name"><em class="fa fa-history mr-1"></em>{{$t('Automatic')}} </span>
                                        </button>
                                    </div>
                                </div>

                            </div>

                        </div>
                            </div>
                            </draggable>
                        

                    </div>
                </div>
           
        </div>
        <AddManualModal v-bind:manualData="trackerData" v-if="addManualShow" v-on:close="addManualShow = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <Comments v-bind:manualData="trackerData" v-if="commentsShow" v-on:close="commentsShow = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <RequestTimeModal v-bind:manualData="trackerData" v-if="requestShow" v-on:close="requestShow = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <Files v-if="ShowManageProjectQuickView" v-on:close="ShowManageProjectQuickView = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />
    <AddNote v-if="showAddNoteModal" v-on:close="showAddNoteModal = false, modalNoteItem = null" v-bind:taskid="TaskId1" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')">
        <h5 slot="header" class="modal-title">
            {{header}}
        </h5>
    </AddNote>
    <NoteListPopUp :userid="this.store.getUserInformation.id" reqfrom="dashboard" v-if="showNoteModal" v-on:close="showNoteModal = false; modalNoteTaskItem = null" v-bind:store="store">
        <h5 slot="header" class="modal-title">
            {{ $t('Notes') }}
        </h5>
    </NoteListPopUp>
    <UpdateProgress v-if="showUpdateProgressModal" v-on:close="showUpdateProgressModal = false" v-bind:store="store" v-on:closeUpdateModal="FetchData('kanban', '')" />

    <modaltracklocation v-if="showTrackLocationModal" v-on:close="showTrackLocationModal = false" v-bind:store="store">
        <h5 slot="header">{{ $t('Location') }}</h5>
    </modaltracklocation>
     </div>
</template>

<style>
div.canvan-view-list { display: flex; flex-wrap: wrap;}
.onlytodo_kb .kvlistview { min-width: 1970px !important;}

.onlytodo_kb .canvan-view-list.kvfixhead div.canceled {flex:1!important; width:380px!important; max-width:380px; list-style-type: none; margin-right: 14px; padding: 0;  border-radius: 0;font-size:14px;} 
.onlytodo_kb .canvan-view-list.kvfixhead div.pending  {flex:1!important; width:380px!important; max-width:380px; list-style-type: none; margin-right: 14px; padding: 0;  border-radius: 0;font-size:14px;} 
.onlytodo_kb .canvan-view-list.kvfixhead div.overdue  {flex:1!important; width:380px!important; max-width:380px; list-style-type: none; margin-right: 14px; padding: 0;  border-radius: 0;font-size:14px;} 
.onlytodo_kb .canvan-view-list.kvfixhead div.upcoming {flex:1!important; width:380px!important; max-width:380px; list-style-type: none; margin-right: 14px; padding: 0;  border-radius: 0;font-size:14px;} 
.onlytodo_kb .canvan-view-list.kvfixhead div.inactive {flex:1!important; width:380px!important; max-width:380px; list-style-type: none; margin-right: 14px; padding: 0;  border-radius: 0;font-size:14px;} 


.onlytodo_kb .canvan-view-list.kvlistview div {flex:1!important; width:380px!important; max-width:380px;}

.canvan-view-list div > h3 span { color: #fff; font-size: 18px; font-weight: normal;}
.canvan-view-list div > h3 { display: flex; justify-content: space-between;  width: 100%; padding: 5px 10px; align-items: center; font-size: 32px; position: sticky; top: 0; z-index: 999;}

/* .canvan-view-list div {list-style-type: none; margin-right: 14px; padding: 0; border-radius: 0; flex-direction: column; font-size: 14px; width: 19%;} */

.spac-eq .Timer {display: inline; width: 49%; min-width: 130px;}


.canvan-view-list div .list-group { height: 100%;}
.canvan-view-list div.card.card-exp {margin-bottom: 10px; border-radius: 0;}

.canvan-view-list div.card-body { padding: 10px;}




</style>

<script>
import DataService from '../../services/DataService'
import moment from 'moment';
import {
    formatter,
    convertSecondToHHMM,
    getSecondsFromDate
} from "@/assets/common/vue-common.js";
import AddManualModal from "../ToDoList/AddManualModal.vue";
import Comments from "../ToDoList/Comments.vue";
import RequestTimeModal from "../ToDoList/RequestTimeModal.vue";

import Files from '../ToDoList/todoListFiles.vue';
import AddNote from "../ToDoList/modalAddNote.vue";
import UpdateProgress from '../ToDoList/updateProgress.vue';
import modaltracklocation from "../ToDoList/modaltracklocation.vue";

import NoteListPopUp from "../ToDoList/modalNoteListPopUp.vue";
import {
    userTimerInfo
} from "../../stores/TimeTrack.js";
import { required } from 'vee-validate/dist/rules';
export default {
    name: "ToDo List Canban View",
    components: {
        AddManualModal,
        Comments,
        RequestTimeModal,
        Files,
        NoteListPopUp,
        AddNote,
        UpdateProgress,
        modaltracklocation,

    },
    props: {
        status:{
            type:String,
            required:false
        },
        module: {
          type: String,
          required: true,
        }
    },
    setup() {
        const store = userTimerInfo();
        return {
            store
        }
    },
    data(){
    return {
            ShowManageProjectQuickView: false,
            showTrackLocationModal: false,
            showUpdateProgressModal: false,
            toDoList: [],
            Timer: '',
            globalTime: '',
            secondsPerMinute: 60,
            minutesPerHour: 60,
            Date: '',
            noRecordToDoList: false,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            projectName: '',
            Status: '',
            //EndDateForSearch: '',
            TotalRecords: 0,
            TRACKED_HOURS: '',
            PROJECT_ID: '',
            TASK_ID: '',
            PROGRESS: '',
            PendingRecords: 0,
            ProgressRecords: 0,
            OverdueRecords: 0,
            UpcomingRecords: 0,
            CompletedRecords: 0,
            cardViewData: [],
            TagData: [],
            TrackId: 0,
            pendingData: [],
            inprogressData: [],
            comData: [],
            MaxCount: 0,
            showNoteModal: false,
            modalNoteTaskItem: null,
            showAddNoteModal: false,
            modalNoteItem: null,
            header: '',
            TaskId1: null,
            ProjectId: null,
            ModuleName: 'TODOLIST',
            ModuleNameAll: '',
            isLoading: false,
            isActiveWeeklyStatus: true,
            trackerData: {
                projectId: 0,
                trackDescription: "",
                taskId: 0,
                isBillable: 0,
                format: false,
                date: null,
                trackId: 0,
                startTime: moment().format("YYYY-MM-DD HH:mm:ss"),
                stopTime: moment().format("YYYY-MM-DD HH:mm:ss"),
                companyId: null,
                currentDate: moment().format("YYYY-MM-DD HH:mm:ss"),
                trackAgent: "web",
                timerStatus: "start",
                CreatedBy: this.store.getUserInformation.id,
                createdBy: this.store.getUserInformation.id,
                linkedTrackId: null,
                totalHours: 0,
                timeformat: 12,
                TagIds: "",
                isForcedUpdate: null,
                type: "TimeTrack",
                trackType: "TimeTrack",
                BreakDate: null,
                BreakName: null,
                BreakId: '',
                BreakStartTime: 0,
                Shift_ID: 0,
                SelectedDate: "",
                Break_Shift: "",
            },
            series: [],
            series1: [],
            chartOptions: {

            },
            chartOptions1: {

            },
            seriesbar: [],
            chartOptionsBar: {},

            addManualShow: false,
            commentsShow: false,
            requestShow: false,
            fromData: {},
            buttonIndex: '',
            isActiveActionButton: false,
            DataArray:[],
            DraggedFromElementStatus: "",
            DraggedToElementStatus: "",
            DraggedElementTaskId: null,
            DraggedElementStatus: null,
        }
    },
    created() {
        // this.status='PENDING,INPROGRESS'
        // this.Status='PENDING,INPROGRESS'
        this.StatusDropdown();
        this.Status=this.status;
        this.FetchData();
        Object.assign(this.fromData, this.trackerData)
        this.store.getTimerInfo();
    },
    mounted() {
        const canbanScroll = document.querySelector('.onlyd-view');
        canbanScroll.addEventListener('scroll', e => {
            if (canbanScroll.scrollTop + canbanScroll.clientHeight >= canbanScroll.scrollHeight) {
                if (this.TotalPages > this.PageNumber) {
                    this.PageNumber = this.PageNumber + 1
                    this.FetchData('scroll')
                }
            }
        })

    },
    watch: {
        canban(newVal, oldVal) {
            this.FetchData('kanban', '');
        },

    },
    methods: {
        StatusDropdown: function () {
            ;

            
            var vm = this;
            //showLoader();

            var url = "langCode=en&statusType=Project&hasGlobal=1";
            DataService.StatusDropdown(url).then(function (response) {
                //hideLoader();
                if (response.data != null && response.data != "") {
                    //vm.StatusData = response.data.result.data;
                    vm.StatusList = response.data.result.data;

                    vm.pendingData = $.grep(vm.StatusList, function (element, index) {
                        return element.STATUS_CODE == 'REFPEN';
                    });
                    vm.inprogressData = $.grep(vm.StatusList, function (element, index) {
                        return element.STATUS_CODE == 'PNPGRS';
                    });
                    vm.comData = $.grep(vm.StatusList, function (element, index) {
                        return element.STATUS_CODE == 'STATUS_DONE';
                    });
                }
            });
        },
        ShowDataArray: function () {
            var vm=this;
            var array = [];
            // array.push({
            //     name: "overdue",
            //     count: 0,
            //     data: [],
            //     show: 0,
            //     className: "canceled",
            //     SortIcon: "fa-sort-alpha-desc",
            //     StatusId:0
            // });
            array.push({
                name: "pending",
                count: 0,
                data: [],
                show: 0,
                className: "pending",
                SortIcon: "fa-sort-alpha-desc",
                StatusId:vm.pendingData[0].STATUS_ID
            });
            array.push({
                name: "inprogress",
                count: 0,
                data: [],
                show: 0,
                className: "overdue",
                SortIcon: "fa-sort-alpha-desc",
                StatusId:vm.inprogressData[0].STATUS_ID
            });
            // array.push({
            //     name: "completed",
            //     count: 0,
            //     data: [],
            //     show: 0,
            //     className: "upcoming",
            //     SortIcon: "fa-sort-alpha-desc",
            //     StatusId:vm.comData[0].STATUS_ID
            // });
            // array.push({
            //     name: "upcoming",
            //     count: 0,
            //     data: [],
            //     show: 0,
            //     className: "inactive",
            //     SortIcon: "fa-sort-alpha-desc",
            //     StatusId: 0
            // });
            this.DataArray = array;
        },

        async FetchData(e) {
            
            var vueObj = this;
            vueObj.isLoading = true;
            let keys = [];
            let values = [];
            if (e != 'scroll') {
                vueObj.DataArray.data = []
                vueObj.toDoList = []
                vueObj.TagData = []
            }
            let url = ''
             url = `pageSize=${this.PageSize}&pageNumber=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&projectId=&type=kanban`;
            if(this.module){
                    url = url + "&moduleNameForSearch=" + this.module;
                    
                }
                else{
                    url = url + "&moduleNameForSearch=''";
                }
                if(this.Status){
                    url = url + "&status=" + this.Status;
                }
                else{
                url = url + "&status=";
                }
                
            await DataService.GetToDoListing(url).then(response => {
                
                var responseData = response.data.result.DATA;
                vueObj.ShowDataArray()
                console.log("StatusId",this.DataArray)
                if (response.data != null && response.data != "") {
                    vueObj.toDoList = response.data.result.DATA;
                    $.each(vueObj.DataArray, function (index, item) {
                                var name = item.name;
                                var data = $.grep(responseData, function (element, index) {
                                        
                                    return element.STATUS.toLowerCase() == name;
                                });
                                var getData = $.grep(vueObj.DataArray, function (element, indexs) {
                                    return element.name == name;
                                });
                                getData[0].data = data;
                                if (typeof (data[0]) != "undefined") {
                                    // getData[0].StatusId = data[0].STATUS_ID;
                                   
                                    
                                    if(getData[0].name==data[0].STATUS)
                                    {
                                        getData[0].show=0
                                    }
                                     else
                                    {
                                        getData[0].show=1
                                    }
                                 if(getData[0].name=='overdue'){getData[0].count = data[0].OVERDUERECORDS }
                                 if(getData[0].name=='pending'){getData[0].count = data[0].PENDINGRECORDS}
                                 if(getData[0].name=='inprogress'){getData[0].count = data[0].PROGRESSRECORDS}
                                 if(getData[0].name=='upcoming'){getData[0].count = data[0].UPCOMINGRECORDS}
                                 if(getData[0].name=='completed'){getData[0].count = data[0].COMPLETEDRECORDS}
                                   
                                }
                               
                     });
                    
                }
                vueObj.TotalPages = Math.ceil(vueObj.toDoList[0].TOTALRECORDS / vueObj.PageSize);
                vueObj.CurrentPage = vueObj.PageNumber;
                vueObj.TagData = response.data.result.TAGDATA;
            })

            
             
        },
        
        OpenCommentModel(item) {
            var vm = this;
            Object.assign(vm.trackerData, vm.fromData)
            vm.trackerData.MESSAGE_BOARD_ID = item.MESSAGE_BOARD_ID
            vm.trackerData.totalHours = item.TRACKED_HOURS
