        UpdateStatusDragDrop: function () {
            var vm = this;
            var canMove = false;
            var draggedFromElementStatus = String(vm.DraggedFromElementStatus).toLocaleLowerCase();
            var draggedToElementStatus = String(vm.DraggedToElementStatus).toLocaleLowerCase();
            console.log("draggedFromElementStatus1",draggedFromElementStatus)
            console.log("draggedToElementStatus1",draggedToElementStatus)
            if (draggedFromElementStatus == "" || vm.DraggedElementTaskId == null) {
                return false;
            }
            if (draggedFromElementStatus == "overdue" && (draggedToElementStatus == "inprogress" || draggedToElementStatus == "completed")) {
                canMove = true;
            }
            else if (draggedFromElementStatus == "pending" && (draggedToElementStatus == "inprogress" || draggedToElementStatus == "completed")) {
                canMove = true;
            }
            else if (draggedFromElementStatus == "inprogress" && draggedToElementStatus == "completed") {
                canMove = true;
            }
            if (canMove) {
                var VarObject = {
                    ID: vm.DraggedElementTaskId,
                    StatusId: vm.DraggedElementStatus,
                    IsFor: "TODOKANBANLISTING"
                };
                var objectPostString = JSON.stringify(VarObject);
                DataService.UpdateStatus(objectPostString).then(function (response) {
                  vm.isLoading=true;
                  console.log("response",response)
                    if (response.data != null) {
                        if (response.data.status === "Success") {
                            //location.reload(true);
                            vm.DataArray.data = [];
                            vm.PageNumber = 1;
                            $("#hdfStartIndex").val(1);
                            $("#hdfEndIndex").val(100);
                            vm.ShowAlert(vm.$t("RecordsUpdatedSuccessfully"), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                        vm.FetchData();
                    }
                    vm.isLoading = false;
                    vm.DraggedElementTaskId = null;
                });
            }
            else {
                vm.DraggedElementTaskId = null;
                vm.FetchData("kanban", null);
                vm.ShowAlert(vm.$t("Dragging"), "warning", true, vm.$t("Alert"));
            }
           vm.isLoading=false;
            //setTimeout(function(){vm.SetScrolbar('dark-thick', null, 'scrollbarmini')}, 500);
        },
