<template>
<div>
    <div class="my-popups">
        <div class="modal d-block ">

            <div class="modal-dialog ui-draggable" style="display: inline-block;">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h5 id="ui-id-26" class="modal-title">{{ $t('AddedManually') }}</h5><button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                        <span class="user-guide">
                            <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button" aria-expanded="false" aria-controls="collapseExample">User Guide</a>
                            <div id="collapseReply" class="divancuserguide collapse">
                                <div id="scrollbarreplyuserguide" class="custom-scrollbar-js" style="overflow: auto;">
                                    <div class="con" v-html="$t('User_Guide_AddMannualy')"></div>
                                </div>
                            </div>
                        </span>
                        <section id="main-content" class="main-content">
                            <section class="main-content">
                                <div class="container-fluid p-0">
                                    <div class="p-0">
                                        <ValidationObserver v-slot="{ handleSubmit }">
                                            <form v-on:submit.prevent="handleSubmit(submit)" ref="form">

                                                <div class="row">

                                                    <div id="divStarttime24" class="col-md-3">
                                                        <div class="form-group">
                                                            <label>{{ $t('EventStartTime') }}:<span class="text-danger">*</span></label>
                                                            <div class="input-group mb-3">
                                                                <vue-timepicker v-model="startTime" format="HH:mm" class="w-100" hide-clear-button @input="getTimeDiff"></vue-timepicker>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div id="divStoptime24" class="col-md-3">
                                                        <div class="form-group">
                                                            <label>{{ $t('StopTime') }}:<span class="text-danger">*</span></label>
                                                            <div class="input-group mb-3">
                                                                <vue-timepicker v-model="endTime" format="HH:mm" class="w-100" hide-clear-button @input="getTimeDiff"></vue-timepicker>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div class="col-md-6">
                                                        <div class="form-group">
                                                            <label>{{ $t('TimeTracked') }}:</label>
                                                            <input type="text" disabled class="DivTimer digitalbg DivTimer_hs form-control" v-model="manualData.totalHours" />
                                                        </div>
                                                    </div>

                                                    <div class="col-md-6">
                                                        <div class="form-group newtimertracked">
                                                            <label>{{ $t('SelectDate') }}:</label>
                                                            <div class="mb-3">

                                                                <DatePicker class="d-flex" v-model="date" :popover="false">
                                                                    <template #default="{ togglePopover, inputValue, inputEvents }">
                                                                        <input placeholder="M/D/YYYY" class="form-control datepicker_HS" :value="inputValue" v-on="inputEvents" />
                                                                        <div class="input-group-append cursor-pointer" v-on:click="togglePopover">
                                                                            <span class="input-group-text">
                                                                                <em class="fa fa-calendar"></em>
                                                                            </span>
                                                                        </div>

                                                                    </template>
                                                                </DatePicker>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div class="col-md-6">
                                                        <div class="form-group"><label for="Billable">{{ $t('Billable') }}</label>
                                                            <div class="form-group"><label class="switch"><input type="checkbox" id="Billable" data-change-id="no" checked="checked" class="form-check-input dynamic custom-control-input" value="" style="display: none;"> <span class="slider round"><span class="slider-yes">{{ $t('Yes') }}</span> <span class="slider-no">{{ $t('No') }}</span></span></label>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-12">
                                                        <div class="form-group"><label for="FormControlTextarea1">{{ $t('communicationTypeDescription') }}:<span class="text-danger">*</span></label>
                                                            <ValidationProvider :rules="'required'" v-slot="{ errors }" class="tt_textarea_span position-relative" name="Description">
                                                                <textarea v-model="manualData.trackDescription" name="Description" class="form-control" :class="errors[0] ? 'is-invalid' : ''" style="border-color: #BDBCBD; resize:none" :placeholder="$t('PleaseEnterDescription')" maxlength="2000" />
                                                                <span class="invalid-feedback">{{ errors[0] }}</span>
                                                                </ValidationProvider>
                                                            </div>
                                                        </div>

                                                    </div>

                                                    <div class="row flex-row-reverse">

                                                        <div class="col-lg-6 text-right">
                                                            <button type="submit" class="btn btn-success"><i
                                                                    class="fa fa-save pr-2"></i>{{ $t('Save') }}</button>
                                                            <button @click="$emit('close')" type="button"
                                                                class="btn btn-danger" style="margin-left: 5px;"><i
                                                                    class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</button>
                                                        </div>

                                                        <div class="col-lg-6"><div class="py-2"><span class="mandatory">{{ $t('MandatoryString') }}</span></div></div>

                                                    </div>

                                                </form>
                                            </ValidationObserver>
                                        </div>
                                    </div>
                                </section>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message"
            v-bind:open="store.getAlertMessage.open" v-on:close="store.$state.getMessageAlert.open = false" />
    </div>
</template>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import {
    extend,
    ValidationObserver
} from 'vee-validate';
import {
    userTimerInfo
} from "../../stores/TimeTrack"
import Alert from '../Common/Alert.vue';
import VueTimepicker from 'vue2-timepicker'
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import {
    getSecondsFromDate,
    formatter,
    dateAndTimeZone,
    dateTimeFromat,
    dateFormat
} from "@/assets/common/vue-common.js"
import moment from "moment"
extend('required', {
    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    message: 'The {_field_} is required'

    ///computesRequired: true
});
export default defineComponent({
    name: "Add Manual Break",
    components: {
        ValidationObserver,
        Alert,
        VueTimepicker,
        DatePicker
    },
    props: {
        manualData: {
            type: Object
        }
    },
    data() {
        return {
            startTime: '00:00',
            endTime: '00:00',
            date: dateAndTimeZone(moment())
        }

    },
    created: function () {
        this.manualData.trackDescription = ''
        this.manualData.totalHours = ''
        console.log("manuall", this.manualData);
    },
    methods: {

        async submit() {
            console.log("test", this.manualData)
            if (this.validHours()) {
                this.store.$state.getMessageAlert = ""
                await this.store.addManualyTimer(this.manualData);
                if (this.store.$state.getMessageAlert.status == 0) {
                    this.$emit('close')
                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    this.store.getTimerListing1(url)
                    const url1 = `dateType=${this.store.$state.dateFilterType}&type=${'list'}&toDate=&fromDate=`
                    this.store.getTrackedtimings(url1)
                }
            }
        },
        validHours() {
            let status = false
            if (!this.startTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": this.$t('PleaseEnterStartDate')
                })
            } else if (!this.endTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": this.$t('PleaseEnterEndDate')
                })
            }
            //console.log("end",moment(moment(this.endTime.HH+':'+this.endTime.mm, 'hh:mm')))
            else if (this.startTime && this.endTime) {

                let dateStart = moment(new Date(this.date), "YYYY-MM-DD").format("YYYY-MM-DD")
                let startDateTime = moment(dateStart + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
                let endDateTime = moment(dateStart + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
                let today = moment(dateAndTimeZone(moment(),'YYYY-MM-DD HH:mm'))
                console.log("today",today)
                // if (startDateTime.valueOf() > endDateTime.valueOf()) {
                //     this.store.alertBOxForMessage({
                //         "type": 'warning',
                //         "open": true,
                //         "message": this.$t('FutureTime')
                //     })
                // } else {
                    status = true
                    let sec = getSecondsFromDate({
                        startDate: moment(this.startTime, 'hh:mm'),
                        endDate: moment(this.endTime, 'hh:mm')
                    }, 'seconds')

                    this.manualData.startDate = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.date = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    //this.manualData.SelectedDate = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.startTime = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.stopTime = endDateTime.format("YYYY-MM-DD HH:mm:ss")
                    //this.manualData.endDate = endDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.totalHours = formatter(sec)
                //}
            }
            return status
        },
        //Get time time diffrece between start date and end date
        getTimeDiff() {
            if (this.startTime && this.endTime) {
                let dateStart = moment(new Date(this.date), "YYYY-MM-DD").format("YYYY-MM-DD")
                let startDateTime = moment(dateStart + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
                let endDateTime = moment(dateStart + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
                let today = moment(dateAndTimeZone(moment(),"YYYY-MM-DD HH:mm"))
                if (startDateTime.valueOf() > endDateTime.valueOf() || endDateTime.valueOf() > today.valueOf()) {
                    this.manualData.totalHours = "00:00:00"
                } else {
                    let sec = getSecondsFromDate({
                        startDate: moment(this.startTime, 'hh:mm'),
                        endDate: moment(this.endTime, 'hh:mm')
                    }, 'seconds')
                    this.manualData.totalHours = formatter(sec)
                }
            }
        }

    },
    setup() {
        const store = userTimerInfo();
        return {
            store,

        }
    },
});
</script>
