<template>
  <div class="w-100 bg-white custom-chart-div t-min-height Linechrt text-center"
    :style="'height: ' + outerDivHeight + 'px;'">
    <div class="graph-widget-content bg-white float-left w-100" :style="'height: ' + outerDivHeight + 'px;'">
      <loader :isVisible="isLoading"></loader>
      <template v-if="LineWidgetData.length === 0 && isLoading === false">
        <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }"
          style="display: flex; align-items: center; justify-content: center">
          {{ $t("NoRecordfound") }}
        </div>
      </template>
      <template v-else>
        <div :id="'lineDiv_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index"
          :style="'height: ' + outerDivHeight + 'px;'">

          <VueApexCharts type="line" :options="chartOptions" height="100%" :series="series"></VueApexCharts>

        </div>
      </template>
    </div>
  </div>
</template>

<script>

import DataService from "../../../services/DataService";
import VueApexCharts from "vue-apexcharts";

export default {
  components: {
    VueApexCharts
  },
  props: {
    reportName: null,
    chartType: String, // Set the type
    // selectedChartType: String,
    dashboardid: Number,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    index: null,
    width: null,
    height: null,
    reportId: null,
    isSystemReport: null,
    resizeheight: null,
    filterData: "",
    xaxisAliasName: null,
    yaxisAliasName: null
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "line",
      xaxis: "",
      yaxis: "",
      LineWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      ChartName: null,

      data: [],
      categories: [],
      series: [],
      chartOptions: {},

      outerDivHeight: 0,

    };
  },
  watch: {
    width: function () {
      this.bindWidget(this);
    },
    height: function () {
      this.bindWidget(this);
    },
    toDate: function () {
      this.FetchData();
    },
    fromDate: function () {
      this.FetchData();
    },
    filterData: function () {
      this.FetchData();
    },
    resizeheight: function () {

      var h = document.getElementById('lineDiv_' + this.moduleCode + '_' + this.action.replace(/\s+/g, '_') + this.index)
      this.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
    }
  },
  created() {
    this.FetchData();
    this.action = this.action.replace(/\s/g, "");
  },
  methods: {
    async FetchData() {

      var vueObj = this;
      vueObj.isLoading = true;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
      await DataService.GetDashboardData(params).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            if (response.data.result != null) {
              vueObj.LineWidgetData = response.data.result.data;
              vueObj.xaxis = response.data.result.Xaxis;
              vueObj.yaxis = response.data.result.Yaxis;
              vueObj.bindWidget(vueObj);


            }
          }
        }
        var h = document.getElementById('lineDiv_' + vueObj.moduleCode + '_' + vueObj.action.replace(/\s+/g, '_') + vueObj.index);
        vueObj.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
        vueObj.isLoading = false;
      });
    },
    bindWidget(vueObj) {


      if (vueObj.charT_TYPE && vueObj.LineWidgetData.length > 0) {

        vueObj.categories = [];
        vueObj.data = [];

        $.each(vueObj.LineWidgetData, function (index, element) {

          vueObj.categories.push(element.NAME);
          vueObj.data.push(element.VALUE);

        });

        vueObj.series = [{
           name: vueObj.xaxisAliasName,
          data: vueObj.data
        }],

          vueObj.chartOptions = {
            chart: {
              //height: 350,
              type: 'line',
              zoom: {
                enabled: false
              }
            },
            dataLabels: {
              enabled: false
            },
            stroke: {
              curve: 'straight'
            },
            title: {
              // text: 'Product Trends by Month',
              align: 'left'
            },
            grid: {
              row: {
                colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                opacity: 0.5
              },
            },
            xaxis: {
              categories: vueObj.categories,
              title: {
                text: vueObj.$t(vueObj.xaxisAliasName),
              }
            },
            yaxis: {
              
              title: {
                text: vueObj.$t(vueObj.yaxisAliasName),
              }
            },

          }
      }
    },
  },
};
</script>


