<template>
    <div class="my-popups" id="MMMM">
        <!-- The Modal -->
        <div class="modal d-block" id="choosewizards">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title">Filter</h4>
                        <button type="button" class="close" data-dismiss="modal" @click="CloseHeaderPopup"><i
                                class="fa fa-times" aria-hidden="true"></i></button>
                    </div>

                    <div class="modal-body">
                        <Form id="fileuploadForMessage" ref="form" v-slot="{ valid, errors }">
                            <div class="listing mt-2">
                                <div class="table-responsive overflow-visible">
                                    <table
                                        class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th style="min-width: 110px; max-width: 110px;"><span>Field Name</span>
                                                </th>
                                                <th style="min-width: 160px; max-width: 110px;"><span>Conditional
                                                        Operator</span></th>
                                                <th style="min-width: 350px;"><span>Value</span></th>
                                                <th class="text-center"><a href="javascript:void(0)" class="text-success"
                                                        v-on:click="addNewRow"><i class="fa fa-plus pr-2"></i>Add</a></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item, index) in searchFilter">
                                                <td>
                                                   
                                                    <select name="fieldName1" data-vv-name="SearchFilter.fieldName1"
                                                        v-model="item.fieldName" @change="GetDropDownValue(item, index)"
                                                        v-bind:class="{ 'form-control disableAria w-100': true, 'error': errors[0] }"
                                                        aria-required="true" aria-invalid="false">
                                                        <option value="" disabled>Select</option>
                                                        <option v-for="(fitem,findex) in FieldList" :value="fitem.value" :disabled="(searchFilter.filter(x=> x.fieldName == fitem.value)).length > 0">
                                                            {{ fitem.name}}
                                                        </option>
                                                    </select>
                                                </td>

                                                <td>
                                                    <select name="operator1" class="form-control disableAria"
                                                        v-model="item.operator" aria-required="true" aria-invalid="false"
                                                        @change="EmptyValue(item)">
                                                        <option value="">Select</option>
                                                        <option v-for="item in Operators" :value="item.expression">{{
                                                            item.name
                                                        }}</option>
                                                    </select>
                                                </td>

                                                <td class="overflow-visible" style="overflow: visible !important;">
                                                    <div class="row">
                                                        <div class="col-12 float-left">
                                                            <select
                                                                v-if="item.operator == '' || item.operator == '=' || item.operator == '<>'"
                                                                name="operator1" class="form-control disableAria"
                                                                aria-required="true" aria-invalid="false"
                                                                v-model="item.fieldValue">
                                                                <option value="">Select</option>
                                                                <option v-for="itm in item.SelectOptionsForValue"
                                                                    :value="itm.value">
                                                                    {{ itm.label }}</option>
                                                            </select>

                                                            <Multiselect v-else class="form-control"
                                                                style="box-sizing:border-box; margin-top: 0px; padding-left: 0px; padding-right:0px;"
                                                                v-model="item.fieldValue" :multiple="true" label="label"
                                                                track-by="value" placeholder="Select" :searchable="true"
                                                                mode="tags" :limit="2" :taggable="false"
                                                                @select="selectionChange" @remove="removeElement"
                                                                :close-on-select="true"
                                                                :options="item.SelectOptionsForValue">
                                                                <span slot="noResult">No Record(s) found.</span>
                                                            </Multiselect>
                                                        </div>
                                                    </div>
                                                </td>

                                                <td class="text-center single-action"><a v-if="index > 0"
                                                        href="javascript:void(0)" class="text-danger"
                                                        v-on:click="deleteRow(index)"><em class="fa fa-trash pr-2"></em></a>
                                                </td>
                                            </tr>

                                        </tbody>
                                    </table>
                                </div>
                                <div class="modal-footer text-right pr-0 pb-0">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">

                                        <a href="javascript:;" class="btn btn-success mr-2" v-on:click="SubmitData()"
                                            :title="$t('Search')">
                                            <em class="fa fa-search pr-2" aria-hidden="true"></em>
                                            {{ $t("Search") }}
                                        </a>
                                        <a href="javascript:;" class="btn btn-primary delete-btn"
                                            v-on:click="CloseHeaderPopup()" :title="$t('Reset')">
                                            <em class="fa fa-times pr-2" aria-hidden="true"></em>
                                            {{ $t("Reset") }}
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import DataService from '../../services/DataService';
export default {
    data() {
        return {
            FieldList: [{
                name: "Biller Assigned",
                value: "BillerName"
            }, {
                name: "Contract",
                value: "Contract"
            }, {
                name: "State",
                value: "State"
            }],
            Operators: [{
                name: "Equals to",
                expression: "="
            }, {
                name: "No Equals to",
                expression: "<>"
            }, {
                name: "Contains",
                expression: "IN"
            }, {
                name: "Not contains",
                expression: "NOT IN"
            }],
            searchFilter: [{
                fieldName: "",
                operator: "",
                fieldValue: "",
                SelectOptionsForValue: []
            }],
            DataValue: [],
            multiselectArray: [],
            modelmultiselect: [],
            FieldValue: '',
            OperatorValue: '',
            FirstSelectValue: '',
            AllSearchValue: [],
            TypeValue: '',
            isResultTrue: true

        }
    },
    props: {
        FilterJsonForEdit: {
            type: Array,
            default: [],
        },
        dashboardid: null,
    },
    created: function () {
        if (this.FilterJsonForEdit.length > 0) {
            this.searchFilter = this.FilterJsonForEdit;
        }
    },
    methods: {
        CloseHeaderPopup() {
            
            var vm = this;
            vm.searchFilter = [{
                fieldName: "",
                operator: "",
                fieldValue: "",
                SelectOptionsForValue: []
            }]
            vm.$parent.addFilters = false;
            vm.$emit('updatefilter', "", vm.searchFilter);
        },
        deleteRow: function (index) {
            this.searchFilter.splice(index, 1)
        },
        addNewRow: function () {
            this.searchFilter.push({
                fieldName: "",
                operator: "",
                fieldValue: "",
                SelectOptionsForValue: []
            })
        },
        GetDropDownValue: function (itm, i) {
            var vm = this;
            // var checkIfExist = vm.searchFilter.filter((y, index) => {
            //     return y.fieldName == itm.fieldName && index != i;
            // })
            // if (checkIfExist.length > 0) {
            //     itm.fieldName = "";
            // } else {
                var type = itm.fieldName;
                var params = '';
                var Module = 'Billing';
                itm.fieldValue = '';
                itm.operator = '';
                itm.SelectOptionsForValue = [];
                if (type == 'Contract') {

                    params = `PageNumber=&PageSize=&Type=BILLING_DASHBOARD&Search=&Id=&ScreenName`;

                    DataService.GetContractList(params).then(function (response) {

                        if (response.data.length > 0) {
                            response.data.forEach((value, index) => {
                                itm.SelectOptionsForValue.push({
                                    value: value.chilD_USER_ID,
                                    label: value.chilD_NAME
                                })
                            })
                        }
                    });
                }
                else if (type == 'State') {

                    params = `CountryId=223&IncludeCanadaState=false`;
                    DataService.GetFilterStateList(params).then(function (response) {

                        if (response.data.length > 0) {
                            response.data.forEach((value, index) => {
                                itm.SelectOptionsForValue.push({
                                    value: value.isO_CODE_3,
                                    label: value.statE_NAME
                                })
                            })
                        }
                    });
                }
                else if (type == 'BillerName') {

                    params = `Type=BILLEDUNBILLED`;

                    DataService.GetBillerList(params).then(function (response) {

                        if (response.data.length > 0) {
                            response.data.forEach((value, index) => {
                                itm.SelectOptionsForValue.push({
                                    value: value.userid,
                                    label: value.username
                                })
                            })
                        }
                    });
                }

            // }
        },
        SubmitData(id) {
            var vm = this;
            let outArray = []
            vm.searchFilter.forEach((value, index) => {
                let val = []
                if (value.fieldName != "" && value.operator != "" && value.fieldValue != "") {
                    
                    let myobj = {
                        fieldName: value.fieldName,
                        operator: value.operator,
                        fieldValue: value.fieldValue
                    }
                    if (value.operator == "IN" || value.operator == "NOT IN") {
                        if (typeof (value.fieldValue.length) == 'undefined') {
                            val.push("'" + value.fieldValue.value + "'");
                        } else {

                            value.fieldValue.forEach(function (x) {
                                val.push("'" + x.value + "'");
                            })
                        }
                    } else {
                        val.push("'" + value.fieldValue + "'");
                    }
                    myobj.fieldValue = val.join(",");
                    outArray.push(myobj);
                    vm.isResultTrue = true;
                }
                else {
                    vm.isResultTrue = false;
                }
            });
            if (vm.isResultTrue == true) {
                var jsondata = JSON.stringify(outArray);
                vm.$emit('updatefilter', jsondata, vm.searchFilter);
                vm.$emit("close");
            }
            else {
                vm.ShowAlert(vm.$t("FillFealds"), "warning", true, vm.$t("Alert"));
            }
        },
        EmptyValue(data) {
            var vm = this;
            data.fieldValue = '';
        },
    }
}
</script>