<template>
  <div class="w-100 bg-white t-min-height donut custom-chart-div text-center" :style="'height: ' + outerDivHeight + 'px;'">
    <div class="graph-widget-content bg-white float-left w-100" :style="'height: ' + outerDivHeight + 'px;'">
      <loader :isVisible="isLoading"></loader>
      <template v-if="DonutWidgetData.length === 0 && isLoading === false">
        <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }"
          style="display: flex; align-items: center; justify-content: center">
          {{ $t("NoRecordfound") }}
        </div>
      </template>
      <template v-else>
        <div :id="'DonutDiv_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index"
          :style="'height: ' + outerDivHeight + 'px;'">

          <VueApexCharts type="donut" :options="chartOptions" height="100%" :series="series"></VueApexCharts>
          <!-- <div style="font-size: 14px; color: #000000">{{xaxisAliasName}}</div> -->
          <div><h6>{{ $t(xaxisAliasName)}}</h6></div>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import VueApexCharts from "vue-apexcharts";
import DataService from "../../../services/DataService";
export default {
  components: {
    VueApexCharts
  },
  props: {
    reportName: null,
    chartType: null,
    dashboardid: null,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    filterData: "",
    index: null,
    width: null,
    height: null,
    reportId: null,
    isSystemReport: null,
    resizeheight: null,
    outerDivHeight: 0,
    xaxisAliasName: null,
    yaxisAliasName: null
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "donut",
      DonutWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      ChartName: null,

      series: [],
      chartOptions: {},
      labels: [],
    };
  },
  watch: {
    width: 'handleResize',
    height: 'handleResize',
    toDate: 'handleFetchData',
    fromDate: 'handleFetchData',
    filterData: 'handleFetchData',
    chartType: 'handleFetchData',
    resizeheight: function () {
      //this.bindWidget(this)
      var h = document.getElementById('DonutDiv_' + this.moduleCode + '_' + this.action.replace(/\s+/g, '_') + this.index)
      this.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
    }
  },
  created: function () {
    var vueObj = this;
    vueObj.FetchData();
  },

  destroyed() {
    // Remove event listener when component is destroyed
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    ViewReport(reportId) {
      const encodedReportID = btoa(JSON.stringify({ ReportID: reportId }));
      const route = '/Report/viewcustomreport/' + encodedReportID;
      // Open the route in a new tab
      window.open(route, '_blank');
    },
    async FetchData() {
      var vueObj = this;
      vueObj.isLoading = true;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var url = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
      await DataService.GetDashboardData(url).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            if (response.data.result != null) {
              vueObj.DonutWidgetData = response.data.result.data;
              // if (vueObj.DonutWidgetData.length > 0) {
              vueObj.bindWidget(vueObj);

              // }
            }
          }
        }
        var h = document.getElementById('DonutDiv_' + vueObj.moduleCode + '_' + vueObj.action.replace(/\s+/g, '_') + vueObj.index);
        vueObj.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
        vueObj.$emit("set-widget-data", vueObj.DonutWidgetData);
        setTimeout(() => {
          vueObj.isLoading = false;
        }, 3000); // 3000 milliseconds = 3 seconds
      });
    },
    bindWidget: function (vueObj) {

      if (vueObj.charT_TYPE && vueObj.DonutWidgetData.length > 0) {
        var chartType = this.charT_TYPE;
        
        vueObj.series = [];
        vueObj.labels = [];
        $.each(vueObj.DonutWidgetData, function (index, element) {
          // if (vueObj.action == "LoggedHoursByProject") {

          vueObj.series.push(element.VALUE);
          vueObj.labels.push(element.NAME);

          // }
        });

        

        vueObj.series = vueObj.series,

          vueObj.chartOptions = {
            chart: {
              type: 'donut',
            },
            
            responsive: [{
              breakpoint: 480,
              options: {
                chart: {
                  //width: 200
                },
                legend: {
                  position: 'bottom'
                }
              }
            }],
            legend: {
              position: 'bottom',
              //show: false,
            },
            labels: vueObj.labels,

          }
      }
    },
    handleResize() {
      this.bindWidget(this);
    },
    handleFetchData() {
      this.FetchData();
    }
  },
};
</script>

</style>