<template>
<div>
    <div class="my-popups">
        <div class="modal d-block ">

            <div class="modal-dialog ui-draggable" style="display: inline-block;">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h5 id="ui-id-26" class="modal-title">{{ $t('Comments') }} {{ commentData.Title }}</h5><button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                        <span class="user-guide">
                            <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button" aria-expanded="false" aria-controls="collapseExample">User Guide</a>
                        <div id="collapseReply" class="divancuserguide collapse">
                        <div id="scrollbarreplyuserguide" class="custom-scrollbar-js">
                        <div class="con" v-html="$t('User_Guide_Comments')" ></div>
                        </div>
                        </div>
                        </span>
                        <section id="main-content" class="main-content">
                            <section class="main-content">
                                <div class="container-fluid p-0">
                                    <div class="p-0">

                                        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
                                            <form v-on:submit.prevent="handleSubmit(submit)" ref="form">

                                                <div class="row">
                                                    <div class="col-md-12">
                                                        <ValidationProvider rules="required" v-slot="{ errors }" class="tt_textarea_span position-relative" name="Comment">
                                                            <label>Comments:<span class="mandatory">*</span></label>
                                                            <textarea :placeholder="$t('WriteAComment')" v-model="commentData.Comment" name="Comment" class="form-control " :class="errors[0] ? 'is-invalid' : ''" style="border-color: #BDBCBD; resize:none" maxlength="2000"></textarea>
                                                            <span class="invalid-feedback">{{ errors[0] ?  $t('CommentIsReq') : ''}}</span>
                                                        </ValidationProvider>

                                                    </div>
                                                </div>

                                                <div class="row flex-row-reverse mt-2">

                                                    <div class="text-end text-right col-12">
                                                        <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                                                        </button>
                                                        <button @click="$emit('close')" type="button" class="btn btn-danger" style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                                                        </button>
                                                    </div>

                                                </div>

                                            </form>
                                        </ValidationObserver>
                                    </div>
                                </div>
                            </section>
                            <div class=" col-12 tab-pane fade active show">
                                            <div v-if="store.getCommentListing.length == 0"
                                                class="row ticket-card-container card-view bg-border p-3">
                                                <div class="col-sm-12 col-md-12 col-lg-12 mt-2 text-center">
                                                    <span class="text-danger ">NoComments</span>
                                                </div>
                                            </div>
                                 </div>
                                 <loader :isVisible="store.getLoadingStatus"></loader>
                            <div class="listing mt-4" id="divconversationbox" v-if="store.getCommentListing!=0">

                                <div class="listing tab-content">
                                    <div class="col-12 d-flex border border-bottom-0 bg-f5f5f5">
                                        <div class="col-md-6 pl-0">
                                            <h6 class="py-3 mb-0">
                                                Comments
                                            </h6>
                                        </div>
                                        <div class="col-md-6 text-right pr-0 bottom_filter_button">
                                            <a href="javascript:;" id="ancreplyExpandAllCollapseAll" class="position-relative" style="z-index:999;" title="" data-toggle-tooltip="tooltip" data-original-title="Expand All or Collapse All" @click="expendOrcollapse">Expand All or Collapse All</a>
                                        </div>
                                    </div>
                                    <div class="tab-pane fade show activity-tab active pt-2" style="overflow-y:auto;max-height:500px;" v-for="(items,index) in store.getCommentListing" :key="index">
                                        <div id="accordion" class="w-100">
                                            <div class="card">
                                                <div class="card-header">
                                                    <a class="card-link" data-toggle="collapse"  @click="toggleCollapse(index)" aria-expanded="isCollapsed(index)">
                                                        <div class="conversation-date heading-border"><span><em v-if="!isCollapsed(index)" class="fa fa-angle-down"></em>
                                                        <em v-else class="fa fa-angle-up"></em>{{ dateFormat(items.dateGroup) }}</span></div>
                                                    </a>
                                                </div>
                                                <div id="collapseOne1requestcomment1" v-if="!isCollapsed(index)" class="collapse show" style="display: block;">
                                                    <div class="card-body" v-for="item,k in items.requestTimeCommentList" :key="k">
                                                        <div class="conversation-main">
                                                            <div class="conversation-time"><span>{{ timeFormat(item.createdAt) }}</span></div>
                                                            <div class="conversation-box-con">
                                                                <div class="conversation-box">
                                                                    <div class="name-circle light-yellow">
                                                                        <img class="img-circle avatar" :src="item.avatar" width="51" height="51" alt="">
                                                                    </div>
                                                                    <div class="conversation-header addednote-ticket">
                                                                        <span>
                                                                            <span class="sender-name">{{ item.userName  }}</span>

                                                                        </span>
                                                                    </div>
                                                                    <div class="conversation-header addednote-ticket">
                                                                        <span>
                                                                            <span class="sender-department wordbreak"> {{ item.comment }}</span>
                                                                        </span>
                                                                    </div>

                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <!-- <div class="conversation-main" v-for="(item,index1) in store.getCommentListing" :key="index1">

                                <div class="conversation-time"><span>{{item.note_date }}</span></div>
                                <div class="conversation-box-con" v-if="!NoRecordForTab">
                                    <div v-bind:class="{ 'conversation-box': true, 'public-note': !item.IS_PRIVATE, 'private-note': item.IS_PRIVATE }">
                                        <div class="name-circle light-grey"><img class="img-circle avatar img-max-width" v-bind:src="(item.PROFILE_PATH != null && item.PROFILE_PATH != '') ? item.PROFILE_PATH : '/Content/images/DefaultImages/uc_user_new.jpg'" alt="" /></div>
                                        <div class="conversation-header">
                                            <span>
                                                <span class="sender-name">{{ item.notes_created_by }}</span>
                                                <span class="sender-department">(<small class="text-danger font-14" v-bind:title="item.note_date | formatDateTime">{{ item.note_date | formatDateTime }}</small>)</span>
                                            </span>

                                        </div>
                                        <div class="conversation-content">
                                            <p v-html="item.crm_notes"></p>
                                        </div>
                                    </div>
                                </div>

                            </div> -->
                        </section>
                    </div>
                </div>

            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
import DataService from '../../services/DataService';
import VueTimepicker from 'vue2-timepicker'
import {
    getSecondsFromDate,
    getPreviousDay
} from "@/assets/common/vue-common.js"
import moment from "moment"
extend('required', {

    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    //message: 'The {_field_} is required'

    ///computesRequired: true
});
export default defineComponent({
    name: "Comments",
    components: {
        ValidationProvider,
        ValidationObserver,
        VueTimepicker,

    },
    props: {
        manualData: {
            type: Object
        },
        store: {
            type: Object
        }
    },
    data() {
        return {
            collapsedSections: [false],
            MessageBoardId:0,
            expendCollapsedSections: [false],
            commentData: {
                Title: '',
                Comment: '',
                MessageBoardId: 0
            },
            messages: {
                MessageBoardContent: '',
                MessageBoardTitle: '',
                RefSourceName : "TaskComment",
                MessageType: 's',
                TaskId: '',
                ProjectId: '',
                UserList:[],
                MessageBoardId: 0,
                type: 'post',
                formData:{}

            },
            requestData: {
                title: '',
                comment: '',
                msgboardid: '',
                projectid: '',
                taskid: '',
                request_time_sec: '',
                file: '',
                clients: '',
                timeInHours: ''
            },

        }

    },
    created: function () {
        this.commentData.Title = this.manualData.TASK_NAME
        this.commentData.MessageBoardId = this.manualData.MESSAGE_BOARD_ID ? this.manualData.MESSAGE_BOARD_ID : 0
        this.store.commetListing(`messageBoardId=${this.commentData.MessageBoardId}&taskId=${this.manualData.taskId}`)
        // console.log("manuall", this.manualData);
        this.toggleCollapse(0)
    },
    methods: {

        isCollapsed(index) {
            // Check if the section at the given index is collapsed
            return this.collapsedSections[index];
        },
        toggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.collapsedSections, index, !this.collapsedSections[index]);
        },
        async submit() {
            if(this.commentData.MessageBoardId>0){
                await this.store.commetsFromClient(this.commentData);
                if (this.store.getCommentResponse.status == "Success") {
                    this.$emit('close') 
                    this.$emit('closeUpdateModal')
                    
                }
            }
            else{
                this.messages.MessageBoardTitle=this.commentData.Title
                this.messages.MessageBoardContent=this.commentData.Comment
                this.messages.ProjectId=this.manualData.projectId
                this.messages.TaskId=this.manualData.taskId
                 DataService.Post(this.messages).then(response => {
                    this.MessageBoardId=response.data.msg
               
                    if(response.data.code=='0')
                    {
                        this.requestData.title=this.commentData.Title
                        this.requestData.comment=this.commentData.Comment
                        this.requestData.msgboardid=this.MessageBoardId
                        this.requestData.projectid=this.manualData.projectId
                        this.requestData.taskid=this.manualData.taskId
                        this.requestData.request_time_sec=0
                         DataService.RequestTime(this.requestData).then(async response=> {
                            if (response.data.status == "Success") {
                                if(response.data.result!= null && response.data.result != ""){
                                    this.commentData.MessageBoardId=this.MessageBoardId
                                     await this.store.commetsFromClient(this.commentData);
                                        if (this.store.getCommentResponse.status == "Success") {
                                            this.$emit('close') 
                                            this.$emit('closeUpdateModal')
                                            
                                        }
                                }
                                
                            }
                        })
                    }

                this.$emit('close')
                this.$emit('closeUpdateModal')
            })
            }
            
            

        },
        dateFormat(value) {
            return getPreviousDay(value)
        },
        timeFormat(value) {
            return moment(value).format('HH:mm')
        },
        expendOrcollapse() {
            let collapsOn = true
            this.collapsedSections.map((item) => {
                if (item) {
                    collapsOn = false
                }
            })
            if (collapsOn) {
                this.collapsedSections = []
                this.store.getCommentListing.map((items, index) => {
                    this.toggleCollapse(index)
                })
            } else {
                this.collapsedSections = []
            }

        }

    },

});
</script>
