<template>
  <div>
    <div
      :id="'DivTimer_' + pgaction"
      :data-pageaction="pgaction"
      class="DatePopUp dynamicdb divtimer_design p-1"
    >
      <table class="table-responsive AssetDatePopUp d-block">
        <tbody class="daterangebody">
          <tr class="daterangetr" ref="daterange">
            <td class="sep daterangetd">
              <ul class="date_select">
                <li id="1">
                  <a
                    :title="$t('Today')"
                    id="aToday"
                    typ="Today"
                    class="Today"
                    @click="ClickTimer('Today', this, pgaction)"
                    >{{ $t("Today") }}</a
                  ><i
                    id="pin1"
                    class="fa fa-thumb-tack mt-1 pinhide inpinner"
                    v-show="showPin"
                    aria-hidden="true"
                  ></i>
                </li>
                <li id="2">
                  <a
                    :title="$t('Yesterday')"
                    id="aYesterday"
                    typ="Yesterday"
                    class="Yesterday"
                    @click="ClickTimer('Yesterday', this, pgaction)"
                    >{{ $t("Yesterday") }}</a
                  ><i
                    id="pin2"
                    class="fa fa-thumb-tack mt-1 inpinner pinhide"
                    v-show="showPin"
                    aria-hidden="true"
                  ></i>
                </li>
                <li id="3">
                  <a
                    :title="$t('thisWeek')"
                    id="aWeek"
                    typ="Week"
                    class="Week"
                    @click="ClickTimer('Week', this, pgaction)"
                    >{{ $t("thisWeek") }}</a
                  ><i
                    id="pin3"
                    class="fa fa-thumb-tack mt-1 pinhide inpinner"
                    v-show="showPin"
                    aria-hidden="true"
                  ></i>
                </li>
                <li id="4">
                  <a
                    :title="$t('AllTime')"
                    id="aAll"
                    typ="AllTime"
                    class="AllTime"
                    @click="ClickTimer('AllTime', this, pgaction)"
                    >{{ $t("thisYear") }}</a
                  ><i
                    id="pin4"
                    class="fa fa-thumb-tack mt-1 pinhide inpinner"
                    v-show="showPin"
                    aria-hidden="true"
                  ></i>
                </li>
              </ul>
            </td>
            <td valign="top" class="daterangetd">
              <ul class="date_select">
                <li id="5">
                  <a
                    id="aLastWeek"
                    class="LastWeek"
                    typ="LastWeek"
                    :title="$t('LastWeek')"
                    @click="ClickTimer('LastWeek', this, pgaction)"
                    >{{ $t("LastWeek") }}</a
                  ><i
                    id="pin5"
                    class="fa fa-thumb-tack mt-1 pinhide inpinner"
                    v-show="showPin"
                    aria-hidden="true"
                  ></i>
                </li>
                <li id="6">
                  <a
                    id="aMonth"
                    class="Month"
                    typ="Month"
                    :title="$t('thisMonth')"
                    @click="ClickTimer('Month', this, pgaction)"
                    >{{ $t("thisMonth") }}</a
                  ><i
                    id="pin6"
                    class="fa fa-thumb-tack mt-1 pinhide inpinner"
                    v-show="showPin"
                    aria-hidden="true"
                  ></i>
                </li>
                <li id="7">
                  <a
                    id="aLastMonth"
                    class="LastMonth"
                    typ="LastMonth"
                    :title="$t('LastMonth')"
                    @click="ClickTimer('LastMonth', this, pgaction)"
                    >{{ $t("LastMonth") }}</a
                  ><i
                    id="pin7"
                    class="fa fa-thumb-tack mt-1 pinhide inpinner"
                    v-show="showPin"
                    aria-hidden="true"
                  ></i>
                </li>
              </ul>
            </td>
          </tr>
        </tbody>
      </table>
      <div
        class="d-flex justify-content-between align-items-end daterangetr"
        style="column-gap: 10px"
      >
        <div class="table_datepickerfix">
          <label>From</label><br />

          <v-date-picker v-model="fromDate" mode="date">
            <template v-slot="{ inputValue, inputEvents }">
              <input
                :id="'txtFromDateReport_' + pgaction"
                name="startDate"
                class="textfield2 ie_crossicon_none FromDate form-control p-1"
                :value="inputValue"
                v-on="inputEvents"
              />
            </template>
          </v-date-picker>
        </div>
        <div class="table_datepickerfix">
          <label>To</label><br />
          <v-date-picker v-model="toDate" mode="date">
            <template v-slot="{ inputValue, inputEvents }">
              <input
                :id="'txtToDateReport_' + pgaction"
                name="endDate"
                class="textfield2 ie_crossicon_none ToDate form-control p-1"
                :value="inputValue"
                v-on="inputEvents"
              />
            </template>
          </v-date-picker>
        </div>
        <div class="table_datepickerfix">
          <input
            type="button"
            :id="'btnsearch_' + pgaction"
            class="btn btn-success float-left p-1 p-1 pl-4 pr-4"
            data-result="btnsearch"
            value="Go"
            @click="searchGoReportclick(this, pgaction,'DateFilterAdded')"
          />
        </div>
      </div>
    </div>
    <input
      type="hidden"
      id="hdnDateSpan"
      value="Month"
      title="hidden ctr for keeping current Search type Like week, month and day."
    />
    <input type="hidden" id="ReportViewer" name="returnType" />
    <input type="hidden" id="pgaction" name="pgaction" :value="pgaction" />
    <input type="hidden" id="hdnViewId" name="viewid" />
    <input type="hidden" id="hdnPinnedViewId" name="viewid" />
  </div>
</template>
<script>
import DataService from "../../services/DataService";
export default {
  props: {
    popupItem:{
      type:Object
    },
    pgaction: null,
    // reportId: null,
  },
  data() {
    return {
      isLoading: false,
      fromDate: null,
      toDate: null,
      eval: "",
      evaltype: "",
      showPin: false,
    };
  },
  created() {
    if(this.popupItem.dateSpan != undefined){
      this.fromDate = this.popupItem.fromDate;
      this.toDate = this.popupItem.toDate;
    }else{
    this.ClickTimer("Week", this, this.pgaction);}
  },
  mounted() {
    $("#DivTimer_global").addClass("d-none");
  },
  methods: {
    searchGoReportclick(id, action,UserAction) {
      var vm = this;
      // $("#hdnViewId").val("");
      // $(".btnPin").hide();
      vm.$emit(
        "set-date-report",
        $("#txtFromDateReport_" + action).val(),
        $("#txtToDateReport_" + action).val(),
        {'UserInputAction':UserAction}
      );
      vm.$parent.closePopup();
    },
    SearchRecord(e, action) {
      var from = action;
      var frmDate = $("#txtFromDate_" + from).val();
      var toDate = $("#txtToDate_" + from).val();
    //   var searchtype = $("#aDateLable_" + from).html();
      var ctrollerName = $(e).closest(".ticketingDashboardHead").attr("dctrl");
      var actionName = $(e).closest(".ticketingDashboardHead").attr("daction");
      var divContainer = $(e)
        .closest(".ticketingDashboardHead")
        .find(".reportBlock");
      $.ajax({
        url: "/" + ctrollerName + "/" + actionName,
        data: { fromDate: frmDate, toDate: toDate },
        type: "get",
        async: true,
        cache: false,
        success: function (data) {
          divContainer.empty();
          divContainer.append(data);
        },
        error: function () {},
      });
    },
    ClickTimer(type, e, action) {
      var vm = this;
      $("#hdnViewId").val($(e).parent().attr("id"));
      if ($("#hdnViewId").val() == $("#hdnPinnedViewId").val()) {
        //$("#idPinView").css("color", "orange");
        $("#idPinView").closest(".btnPin").addClass("pinmyview");
        $("#idPinView").closest(".btnPin").removeClass("unpinmyview");
      } else {
        //$("#idPinView").css("color", "white");
        $("#idPinView").closest(".btnPin").addClass("unpinmyview");
        $("#idPinView").closest(".btnPin").removeClass("pinmyview");
      }
      $(".btnPin").show();
      /*alert($("#hdnViewId").val());*/
      if ($(e).closest("div.DatePopUp").attr("data-pageaction") == "global") {
        $(e)
          .closest(".date_range")
          .find("#aDateLable_global")
          .find("em.text-white")
          .html("")
          .html($(e).text());
        vm.isLoading = true;
        vm.FadeInOut1(e);
        $("#dashbord-graph-widget")
          .find(".ticketingDashboardHead")
          .each(function () {
            $(this)
              .find(".DatePopUp")
              .find("a[class=" + type + "]")
              .trigger("click");
            $(this).find(".DatePopUp").hide();
          });
        if (type == "Today") {
          vm.GetStartEndDates(type, e, action);
        } else if (type == "Yesterday") {
          vm.GetStartEndDates("yesterday", e, action);
        } else if (type == "Week") {
          vm.GetStartEndDates("thisweek", e, action);
        } else if (type == "LastWeek") {
          vm.GetStartEndDates("lastweek", e, action);
        } else if (type == "Month") {
          vm.GetStartEndDates("thismonth", e, action);
        } else if (type == "LastMonth") {
          vm.GetStartEndDates("lastmonth", e, action);
        } else if (type == "AllTime") {
          vm.GetStartEndDates("thisyear", e, action);
        }
        vm.isLoading = false;
      } else {
        if (type == "Today") {
          $(e).closest(".date_range").find("#hdnDateSpan").val("day");
          vm.GetStartEndDates(type, e, action);
        } else if (type == "Yesterday") {
          $(e).closest(".date_range").find("#hdnDateSpan").val("day");
          vm.GetStartEndDates("yesterday", e, action);
        } else if (type == "Week") {
          $(e).closest(".date_range").find("#hdnDateSpan").val("week");
          vm.GetStartEndDates("thisweek", e, action);
        } else if (type == "LastWeek") {
          $(e).closest(".date_range").find("#hdnDateSpan").val("week");
          vm.GetStartEndDates("lastweek", e, action);
        } else if (type == "Month") {
          $(e).closest(".date_range").find("#hdnDateSpan").val("month");
          vm.GetStartEndDates("thismonth", e, action);
        } else if (type == "LastMonth") {
          $(e).closest(".date_range").find("#hdnDateSpan").val("month");
          vm.GetStartEndDates("lastmonth", e, action);
        } else if (type == "AllTime") {
          $(e).closest(".date_range").find("#hdnDateSpan").val("day");
          vm.GetStartEndDates("thisyear", e, action);
        }
        vm.FadeInOut1(e);
      }
      $(e).closest(".date_range").find("#imgNext").show();
      $(e).closest(".date_range").find("#imgPrev").show();
    },
    FadeInOut() {
      $("#DivTimer").toggle();
    },
    FadeInOut1(e) {
      var nm = $(e).closest(".date_range").find("#pgaction").val();
      $(e)
        .closest(".date_range")
        .find("#DivTimer_" + nm)
        .toggle();
      //$("input[id^='DivTimer_']").toggle();
    },
    GetStartEndDates(dateSpan, e, action) {
      var vm = this;
      vm.isLoading = true;
      var params = `DateSpan=${dateSpan.toLowerCase()}&report=${$("#hdnGroupBy").val()}`;
      DataService.GetStartEndDate_New(params).then(function (response) {
        vm.isLoading = false;
        var data = response.data;
        vm.fromDate = data.dateStart;
        vm.toDate = data.dateEnd;
        $("#txtFromDate_" + action).val(data.dateStart);
        $("#txtToDate_" + action).val(data.dateEnd);
        $("#aDateLable_" + action)
          .find("em")
          .text(data.displayText);
        // vm.$emit("set-date-report", data.dateStart, data.dateEnd);
      });
    },
  },
};
</script>
<style scoped>
.DatePopUp {
  position: relative !important;
}
.DatePopUp.dynamicdb {
  width: 320px;
  top: 0 !important;
}

.divtimer_design table.AssetDatePopUp {
  margin-bottom: 0;
}

.divtimer_design table.AssetDatePopUp td.daterangetd {
  padding: 0 !important;
  text-align: left;
  vertical-align: top;
  font-size: 12px;
}

.divtimer_design table.AssetDatePopUp td.sep {
  border-right: 1px solid #d2d2d2;
}

.reporthead .divtimer_design table.AssetDatePopUp td.daterangetd {
  line-height: 30px;
}

.date_range_bi div.divtimer_design span {
  margin-top: 0 !important;
  color: #000;
}

.divtimer_design .bootstrap-datetimepicker-widget {
  top: auto !important;
  left: 5px !important;
}

.AssetDatePopUp tr td.daterangetd.date-range-column {
  max-width: 114px !important;
  display: table-column !important;
  width: auto !important;
}

.AssetDatePopUp tbody.daterangebody tr.daterangetr {
  display: table-row !important;
}
.daterangebody {
  display: grid !important;
}
</style>
