<script>
import ModalAddDashboard from './ModalAddDashboard.vue';
import ModalAddReport from './ModalAddReport.vue';
import ModalShareReport from './ModalShareReport.vue';
import DataService from '../../services/DataService';
import ReportLayout from './ReportLayout.vue';
import daterange from './DateRange.vue';
import AddFilter from './AddFilter.vue'
export default {
    components: {
        ModalAddDashboard,
        ModalAddReport,
        ModalShareReport,
        ReportLayout,
        daterange,
        AddFilter
    },
    data: function () {
        return {
            selectedDashboardName: '',
            cursorPointer: false,
            isLoading: false,
            showAdddashboard: false,
            showAddReport: false,
            shareReportKey: false,
            showShareReport: false,
            isOwnerKey : false,
            isDefaultKey : false,
            dashboards: [],
            dashboardid: 0,
            reports: [],
            showFilters: false,
            showpopup: false,
            StateList: [],
            fromDate: null,
            toDate: null,
            calenderSelect: "",
            clientByCompany: [],
            selectedClient: [],
            isdraggable: false,
            isresizable: false,
            // responsive: true,
            isDropdownOpen: false,
            editOrSave: 'Edit',
            DeleteButtonClass:'fa fa-trash',
            ShareButtonClass:'fa fa-share',
            SaveEditButtonClass:'fa fa-edit',
            SaveDefaultClass:'fa fa-floppy-o',
            displayEditButton:false,
            addFilters:false,
            moduleCode: (this.$route.params.moduleCode.toLowerCase() == 'index' ? 'CRM': this.$route.params.moduleCode),
            filterData: "",
            FilterJsonForEdit: [],
            SelectedDashboardItem: false,
        }
    },
    created: function () {
        this.GetDashboard();
        this.GetStateListByCountryCode();
        this.GetParentClientByCompanyIdForGroup_New();
    },
    computed: {
        computedSelectedDashboardName() {
            const selectedDashboard = this.dashboards.find(dashboard => dashboard.dashboard_id === this.dashboardid);
            return selectedDashboard ? selectedDashboard.dashboard_name : '';
        }
    },
    watch: {
        fromDate: 'handleDashboardData',
        toDate: 'handleDashboardData',
        filterData: 'handleDashboardData',
        computedSelectedDashboardName(newValue) {
            this.selectedDashboardName = newValue;
        },
    },
    methods: {
        makeDefault() {
            var vm = this;
            // vm.isLoading = true;
            vm.confirmR(vm.$t("ConfirmModal"), true, vm.$t("Confirm"), function() {
                var params = `dashboardId=${vm.dashboardid}&moduleCode=${vm.moduleCode}`;
                DataService.SaveDashboardDefaultReport(params).then(async function(response) {
                    if (response.data.status) {
                        vm.ShowAlert(vm.$t("AddedDefault", [vm.$t("Dashboard")]),"success",true,vm.$t("Alert"));
                        await vm.GetDashboard();
                    } else {
                        vm.ShowAlert(
                            vm.$t("Failure", [vm.$t("Dashboard")]),
                            "warning",
                            true,
                            vm.$t("Alert")
                        );
                    }
                    // vm.isLoading = false;
                });
            })
        },
        removeDefault() {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmModal"), true, vm.$t("Confirm"), function() {
                var params = `dashboardId=${vm.dashboardid}&moduleCode=${vm.moduleCode}`;
                DataService.RemoveDashboardDefaultData(params).then(async function(response) {
                    if (response.data.status) {
                        vm.ShowAlert(vm.$t("RemoveDefaultDashboard", [vm.$t("Dashboard")]),"success",true,vm.$t("Alert"));
                        await vm.GetDashboard();
                    } else {
                        vm.ShowAlert(
                            vm.$t("Failure", [vm.$t("Dashboard")]),
                            "warning",
                            true,
                            vm.$t("Alert")
                        );
                    }
                    // vm.isLoading = false;
                });
            })
        }, 
        updateSelectedOptions() {
            this.selectedClient = this.clientByCompany.filter(option => option.selected);
        },
        SetDateRange(from, to, type) {
            this.fromDate = from;
            this.toDate = to;
            this.calenderSelect = type;
        },
        OpenDatePopUp() {
            this.showpopup = !this.showpopup;
            // eslint-disable-next-line no-undef
            $('#DivTimer_global').toggleClass('d-none');
        },
        openFilters() {
            this.showFilters = !this.showFilters;
        },
        addFilterss(){
            this.addFilters = true;
        },
        opendashborad() {
            this.showAdddashboard = !this.showAdddashboard;
        },
        openReport() {
            this.showAddReport = !this.showAddReport;
        },
        editreport(cancel){
            var vm = this; 
            if(vm.editOrSave == 'Save' && cancel != true){
                vm.cursorPointer = true;
                vm.SaveEditReport();
            }
            vm.SaveEditButtonClass = vm.editOrSave == 'Edit' ? 'fa fa-save' : 'fa fa-edit' ;
            vm.editOrSave = vm.editOrSave == 'Edit' ? 'Save' : 'Edit' ;
            this.$refs.reportLayoutref.updateChildVariables();            
        },
        SaveEditReport(){
            var vm = this;  
            if(vm.editOrSave == 'Save'){
                this.$refs.reportLayoutref.SaveEditedChanges();
            }
        },
        shareReport() {
            this.showShareReport = true;
        },
        deleteReport() {
            var vm = this;
            vm.dashboardid
            var data = {
                ids: vm.dashboardid ,
                // companyId: vm.companyId,
                // userId: vm.user_id,
                // moduleName: "Dynamic_Dashboard",
                // subModuleCode: "Dynamic_Dashboard",
                refCode: "Dynamic_Dashboard"

            };
            var objectPostString = JSON.stringify(data);
            vm.confirmR(
                vm.$t("ConfirmModalDelete"),
                true,
                vm.$t("Confirm"),
                function() {
                    DataService.deleteActivityData(objectPostString).then(response => {
                    // DataService.deleteActivityData(objectPostString).then(response => {
                    if (response.data[0].Status == "Success") {
                        vm.getDefaultDashboard();
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Dashboard')]), "success", true, vm.$t('Alert'));
                    }
                    else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Dashboard')]), "failure", true, vm.$t('Alert'));
                    }
                });    
            });
        },
        async getDefaultDashboard () {
            var vm = this;
            vm.isLoading = true;
            if(vm.dashboardid > 0) {
                vm.dashboardid = "0";
            }
            vm.GetDashboard(vm.dashboardid);
            vm.isLoading = false;
        },
        async GetDashboard(id) {
            var vm = this;
            var params = `moduleCode=${this.moduleCode}`;
            vm.isLoading = true;
            await DataService.GetDashboard(params).then(response => {
                let dashboardData = JSON.parse(response.data)
                vm.dashboards = dashboardData.DATA;
                if (vm.dashboards.length > 0) {
                    let defaultDashboard = vm.dashboards.filter((x) => { return x.is_default == true})
                    if (defaultDashboard !== null && defaultDashboard !== '' && id === undefined) {
                        vm.dashboardid = defaultDashboard.length > 0 ? defaultDashboard[0].dashboard_id : vm.dashboardid = 0;
                    } else {
                        vm.dashboardid = id;
                    }
                } 
                vm.onchangeView();
            });
            setTimeout(() => {
                vm.isLoading = false;
            }, 6000); // 6000 milliseconds = 6 seconds
            
        },
        updateReportObject() {
            // Update the report with fromDate and toDate
            if (this.fromDate && this.toDate) {
                this.reports.forEach(report => {
                    report.fromDate = this.fromDate;
                    report.toDate = this.toDate;
                    report.filterData = this.filterData
                });
            }
        },
        async onchangeView () {
            var vm = this;
            var url = `dashboardId=${vm.dashboardid}&moduleName=${vm.moduleCode}`
            vm.reports = [];
            await DataService.GetDashboardReport(url).then(function (response) {
                var data = response.data;
                vm.GetFirstvalofJson();
                vm.reports = data;
                vm.updateReportObject();
            });
            if(vm.displayEditButton && (vm.editOrSave == 'Save')) {
                vm.cancelEdit();
                this.$refs.reportLayoutref.updateChildVariables();
            }
        },
        GetFirstvalofJson : function(){
            
            var vm = this;
            if(vm.dashboardid > 0) {
                const foundItem = vm.dashboards.find(item => item.dashboard_id == vm.dashboardid);
                vm.SelectedDashboardItem = foundItem.is_todo;
                vm.displayEditButton = foundItem.is_editable;
                vm.isDefaultKey = foundItem.is_default;
                vm.shareReportKey = foundItem.is_shareable;
                vm.isOwnerKey = foundItem.is_owner;
            } else {
                vm.displayEditButton = false;
                vm.shareReportKey = false;
                vm.isOwnerKey = false;
            }
        }, 
        cancelEdit(id){
            var vm = this;
            vm.cursorPointer = true;
            vm.editreport(true);
            vm.GetDashboard(id);
        },
        GetStateListByCountryCode: function () {
            var vm = this;
            var url = `countryId=223&includeCandaState=true`
            DataService.GetStateListByCountryCode(url).then(response => {
                vm.StateList = response.data;
            });
        },
        GetParentClientByCompanyIdForGroup_New: function () {
            var vm = this;
            DataService.GetParentClientByCompanyIdForGroup_New().then(response => {
                vm.clientByCompany = response.data;
            });
        },
        toggleMenu(event) {
            // eslint-disable-next-line no-undef
            $(event.target).parent().parent().find('.dropdown-menu').toggleClass('d-block');
        },
        handleDashboardData() {
            this.GetDashboard(this.dashboardid);
        },
        updateFilter: function(jsonData, actualObj){
            this.filterData = jsonData;
            this.FilterJsonForEdit = actualObj;
        }
    }
}
</script>
<template>
    <section class="page-action">
        <div class="container-fluid px-0" :class="{'cursor-pointer' : cursorPointer}">
            <div class="row">
                <input type="hidden" value="@titleName" id="hdnModuleName" />
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition p-actions-expand d-flex justify-content-between" style="overflow: visible">
                        <div class="d-flex">
                            <div title="dashboard" class="p-name text-white px-2">
                                <select class="form-control customized_ddl" name="dashsbords" v-model="dashboardid" @change="onchangeView()" v-if="editOrSave == 'Edit'">
                                    <option value="0">Default Dashboard</option>
                                    <option v-for="item in dashboards" :value="item.dashboard_id" :key="item">{{ item.dashboard_name }} </option>
                                </select>
                                <input v-else
                                    v-bind:class="{ 'form-control': true }"
                                    id="dashboardNameEdit"
                                    name="dashboardNameEdit"
                                    maxlength="200"
                                    type="text"
                                    v-model="selectedDashboardName"
                                />
                            </div>
                            <div class="p-actions" v-if="dashboardid > 0">
                                <button class="p-action-btn text-white link" v-if="isDefaultKey != true && editOrSave == 'Edit'" @click="makeDefault">
                                    <i :class="SaveDefaultClass" aria-hidden="true"></i>
                                    <br> {{ $t('Make_Default') }}
                                </button>
                                <button class="p-action-btn text-white link" v-else @click="removeDefault" v-if="isDefaultKey == true && editOrSave == 'Edit'">
                                    <i :class="SaveDefaultClass" aria-hidden="true"></i>
                                    <br> {{ $t('RemoveDefault') }}
                                </button>
                                <button class="p-action-btn text-white link" v-if="shareReportKey && editOrSave == 'Edit'" @click="shareReport">
                                    <i :class="ShareButtonClass" aria-hidden="true"></i>
                                    <br> {{ $t('Share') }}
                                </button>
                                <button class="p-action-btn text-white link" v-if="isOwnerKey && editOrSave == 'Edit'"  @click="deleteReport">
                                    <i :class="DeleteButtonClass" aria-hidden="true"></i>
                                    <br> {{ $t('Delete') }}
                                </button>
                            </div>
                        </div>
                        <div class="p-actions">
                            <button class="p-action-btn text-white" id="teamview" @click="addFilterss()" v-if="moduleCode == 'Billing' && editOrSave == 'Edit'">
                                <i class="fa fa-filter" aria-hidden="true"></i>
                                <br>{{ $t('Filter') }}
                            </button>
                            <button class="p-action-btn text-white link" v-if="displayEditButton && dashboardid > 0" @click="editreport">
                                <i :class="SaveEditButtonClass" aria-hidden="true"></i>
                                <br> {{ editOrSave }}
                            </button>
                            <!-- <a href="javascript:;" class="p-action-btn text-white link" @click="openFilters"><i
                                    class="fa fa-filter" aria-hidden="true"></i><br> {{ $t('Filters') }} </a> -->
                            <button class="p-action-btn text-white link" @click="cancelEdit(dashboardid)" v-if="(displayEditButton && (editOrSave == 'Save'))" >
                                <i class="fa fa-times" aria-hidden="true"></i>
                                <br> {{ $t('Cancel') }}
                            </button>
                            <button class="p-action-btn text-white" id="teamview" @click="opendashborad()" v-if="editOrSave == 'Edit'">
                                <i class="fa fa-dashboard" aria-hidden="true"></i>
                                <br>{{ $t('Add Dashboard') }}
                            </button>
                            <button class="p-action-btn text-white" id="teamview" @click="openReport()" v-if="editOrSave == 'Edit' && dashboardid > 0 && displayEditButton">
                                <i class=" fa fa-plus" aria-hidden="true"></i>
                                <br>{{ $t('AddReport') }}
                            </button>
                            <button class="p-action-btn text-white" id="teamview" v-if="editOrSave == 'Edit'" @click="OpenDatePopUp($event)">
                                <i class="fa fa-calendar" aria-hidden="true"></i>
                                <br>{{ GetLocalizedValue(calenderSelect) }}
                            </button>
                            
                            <daterange pgaction="global" :showDateRange="showpopup" @set-date="SetDateRange"></daterange>
                        </div>

                        <div :class="{ 'action-list-box2 expand': true, 'd-none': showFilters == false }">
                            <span class="p-2">
                                <Multiselect placeholder="Select State" :searchable="true" :options="StateList"
                                    track-by="value" label="name" />
                            </span>
                            <span class="p-2">
                                <div class="dropdown" @click="isDropdownOpen = !isDropdownOpen">
                                    <div class="selected-options">
                                        <span v-if="selectedClient.length === 0" role="button"
                                            class="form-control dropdown-toggle" data-toggle="dropdown"
                                            @click="toggleMenu($event)">Select options</span>
                                        <span v-else-if="selectedClient.length === 1" role="button"
                                            class="form-control dropdown-toggle" data-toggle="dropdown"
                                            @click="toggleMenu($event)">{{ selectedClient[0].text }}</span>
                                        <span v-else role="button" class="form-control dropdown-toggle"
                                            data-toggle="dropdown" @click="toggleMenu($event)">{{ selectedClient.length }}
                                            options selected</span>
                                    </div>
                                    <div class="dropdown-menu" role="menu" aria-labelledby="dLabel" v-show="isDropdownOpen">
                                        <div class="option" v-for="option in clientByCompany" :key="option.value">
                                            <label>
                                                <input type="checkbox" :value="option.value" v-model="option.selected"
                                                    @change="updateSelectedOptions">
                                                {{ option.text }}
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </span>

                            <span class="p-2">
                                <select class="form-control">
                                    <option value="">{{ $t('Biller') }} {{ $t('Assigned') }}</option>
                                </select></span>

                            <div class="row flex-row-reverse mt-2">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" id="aSearch" :title="$t('Search')" name="aSearch"
                                        class="btn btn-success formbtn text-white mr-1">
                                        <i class="fa fa-search"></i></a>
                                    <a href="javascript:;" :title="$t('Reset')" id="aClear"
                                        class="btn btn-danger formbtn widthhalf btnCancel">
                                        <i class="fa fa-refresh"></i></a>

                                </div>
                            </div>
                        </div>

                        <span class="collapse-head-btns">
                            <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                        </span>
                    </div>
                    <ReportLayout v-if="reports.length > 0 && fromDate" :reports="reports" :moduleCode = "moduleCode" :dashboardid="dashboardid" :selectedDashboardName="selectedDashboardName" :isTodo = "SelectedDashboardItem"  ref="reportLayoutref"></ReportLayout>
                </div>
            </div>
            <ModalAddDashboard v-if="showAdddashboard" :dashboardid="dashboardid" :moduleCode = "moduleCode"></ModalAddDashboard>
            <ModalAddReport v-if="showAddReport" :dashboardid="dashboardid" :moduleCode = "moduleCode"></ModalAddReport>
            <ModalShareReport v-if="showShareReport" @close="showShareReport = false;" :dashboardid="dashboardid" :moduleCode = "moduleCode"></ModalShareReport>
            <AddFilter v-if="addFilters" @close="addFilters = false;" :FilterJsonForEdit="FilterJsonForEdit" :dashboardid="dashboardid" @updatefilter="updateFilter"></AddFilter>
        </div>
        <loader :isVisible="isLoading"></loader>
    </section>
</template>
<style>
.action-list-box2 {
    position: absolute;
    top: 53px;
    right: 145px;
    width: 400px;
    overflow: hidden;
    display: inline-block;
    padding: 5px;
    white-space: nowrap;
    z-index: 99;
    display: block;
    background: #fff;
    border: 1px solid #bcbcbc;
}

.action-list-box2 span {
    display: block;
}
.vue-grid-item {
    border: 1px solid #cbcbcb !important;
}
.edit-mode .vue-grid-item {
    border: 1px solid #f58181 !important;
}
.edit-mode .vue-grid-layout{
    background-color: #f5e8da !important;
}
.edit-mode .fa.fa-times {
    color: #ff0606 !important;
}
</style>
<style scoped>
.cursor-pointer {
    cursor: default!important;;
}
.p-action-btn {
    border-left: 2px solid #495a62;
    border-right: 0;
    background-color: #525f65;
    border-top: 0;
    border-bottom: 0;
    height: auto !important;
}
.p-action-btn:hover {
    border-left: 2px solid #495a62;
    border-right: 0;
    background-color: #4a585e;
    border-top: 0;
    border-bottom: 0;
}
</style>