<template>
    <div class="w-100 bg-white custom-chart-div t-min-height Tablechrt text-center">
      <template v-if="OpenTicketsWidgetData.length === 0 && isLoading === false">
        <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }" style="display: flex; align-items: center; justify-content: center">
          {{ $t("NoRecordfound") }}
        </div>
      </template>
      <template v-else>
        <div :id="'div_TableChart_' + action.replace(/\s+/g, '_') + index" class="listing" v-if="OpenTicketsWidgetData">
          <loader :isVisible="isLoading"></loader>
          <div class="listing">
            <div class="graph-widget align-self-stretch pl-0" :class="{ scrollable: isTableResponsive }" :style="{ maxHeight: (height * 2) - 100 + 'px', overflow: 'auto' }">
              <table class="table table-bordered dt-responsive nowrap mt-0">
                <thead class="thead-bg">
                  <tr>
                    <th scope="col" v-for="(item, index) in items" :key="index">
                      {{ GetLocalizedValue(item) }}
                      
                    </th>
                  </tr>
                </thead>
                <tbody v-if="IsGrouping == true">
                  <tr v-for="(item, index) in OpenTicketsWidgetData" :key="index">
                    <td>{{ item[0] }}</td>
                    <td>{{ item[1] }}</td>
                    <td>
                      <div v-for="(subGroup, subIndex) in item.slice(2)" :key="subIndex">
                        {{ capitalize(subGroup[1]) }}
                      </div>
                    </td>
                    <td>
                      <div v-for="(subGroup, subIndex) in item.slice(2)" :key="subIndex">
                        {{ capitalize(subGroup[1]) }}
                      </div>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else-if="IsGrouping == false">
                  <tr v-for="(item, index) in OpenTicketsWidgetData" :key="index">
                    <td v-for="(value, key) in item" :key="key">
                      <span v-html="value"></span>
                    </td>
                  </tr>
                </tbody>
                <tbody v-else>
                  <tr v-for="(item, index) in OpenTicketsWidgetData" :key="index">
                    <td v-for="(value, key) in item" :key="key">
                      {{ value }}
                    </td>
                  </tr>
                </tbody>

                
              </table>

             
            </div>
            <!-- <i v-if="PageNumber != 1" class="fa fa-angle-double-left" @click="Decrease" aria-hidden="true"></i>

            <i v-if="TotalRecord > PageCalculate" class="fa fa-angle-double-right" @click="Increase" aria-hidden="true"></i> -->
            <!-- <button v-if="PageNumber != 1" @click="Decrease">Prev</button>
            <button v-if="TotalRecord > PageCalculate" @click="Increase">Next</button> -->
          </div>
        </div>
      </template>
  </div>
</template>

<script>
import DataService from "../../../services/DataService";

export default {
 
  props: {
    reportName: null,
    chartType: String, // Set the type
    // selectedChartType: String,
    dashboardid: Number,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    index: null,
    width: null,
    height: null,
    tableheight: null,
    reports: null,
    reportId: null,
    isSystemReport: null,
    filterData: "",
  },
  data: function () {
    return {
      isLoading: false,
      action: this.reportName,
      charT_TYPE: "table",
      OpenTicketsWidgetData: [],
      HeaderData: null,
      items: [],
      IsGrouping: false,
      showAddDashboardReport: false,
      //PageNumber:1,
      TotalRecord: 0,
      PageCalculate:0,
    };
  },
  created: function () {
    this.FetchData();
    console.log(this.tableheight);
  },
  watch: {
    height: 'handleResize',
    toDate: 'handleFetchData',
    fromDate: 'handleFetchData',
    chartType: 'handleFetchData',
    filterData: 'handleFetchData',
    //PageNumber: 'handleFetchData',
  },
  // computed: {
  //   isTableResponsive() {
  //     console.log(this.calculateTableHeight() > this.height);
  //     // return this.calculateTableHeight() > this.tableheight;
  //     // const numRows = this.OpenTicketsWidgetData.length;
  //     // return numRows > this.tableheight;
  //   },
  // },
  methods: {
    capitalize(value) {
      // Assuming that value is a string, capitalize the first letter
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
    // calculateTableHeight() {
    //   const numRows = this.OpenTicketsWidgetData.length;
    //   const rowHeight = 10; // Set your average row height here
    //   return numRows * rowHeight;
    // },
    handleResize() {
      console.log(this.height);
    },
    async FetchData() {
      
      var vueObj = this;
      vueObj.OpenTicketsWidgetData = [];
      vueObj.isLoading = true;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
      await DataService.GetDashboardData(params).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            if(response.data.result.data != null && response.data.result.data != "") {
              vueObj.HeaderData = response.data.result.data;
              if (response.data.result.data.Xaxis != null || response.data.result.Yaxis != null) {
                var xCategory = response.data.result.Xaxis;
                var yCategory = response.data.result.Yaxis;
                vueObj.HeaderData = response.data.result.data;
                var categoryArray = [];
                var arrAU = [];
                $.each(vueObj.HeaderData, function (index, element) {
                  categoryArray.push(element.NAME);
                  arrAU.push(element.VALUE);
                });
                vueObj.items = [xCategory, yCategory];
                vueObj.OpenTicketsWidgetData = categoryArray.map((listing, index) => ({ listing, count: arrAU[index] }));
                console.log(vueObj.OpenTicketsWidgetData);
              } else 
              {
                // Parse the JSON within the VALUE property
                vueObj.HeaderData.map((item) => {
                  if (item.IsGrouping == false) {
                    vueObj.IsGrouping = false;
                    vueObj.TotalRecord = item.Total;
                    const valueArray = JSON.parse(item.VALUE);
                    const extractedValues = valueArray.map((entry) =>
                      JSON.parse(entry.Value)
                    );
                    // Split keys and values for each object
                    if(extractedValues.length > 0) {
                      vueObj.items = Object.keys(extractedValues[0]);
                      extractedValues.forEach((item) => {
                        const values = Object.values(item);
                        vueObj.OpenTicketsWidgetData.push(values);
                      });
                    }
                  } else if (item.IsGrouping == true) {
                    vueObj.IsGrouping = true;
                    vueObj.TotalRecord = item.Total;
                    const valueArray = JSON.parse(item.VALUE);
                    // console.log(valueArray);
                    const extractedValues = valueArray.map((entry) =>JSON.parse(entry.Value));
                    // console.log(extractedValues);
                    // Assuming all objects in extractedValues have the same structure
                    const commonKeys = Object.keys(extractedValues[0]);
                    // Filter out the "data" property
                    const dataKey1 = commonKeys.filter((key) => key !== "data");
                    // Extract keys from the "data" property
                    const dataKey2 = Array.from(
                      new Set(
                        extractedValues.flatMap((item) =>
                          item.data ? item.data.flatMap((data) => Object.keys(data)) : []
                        )
                      )
                    );
                    // Combine all keys
                    vueObj.items = [...dataKey1, ...dataKey2];
                    // console.log(vueObj.item);
                    // Process each item in extractedValues
                    extractedValues.forEach((item) => {
                      // Remove the "data" property from each item
                      const { data, ...newObject } = item;
                      // Combine values and push to OpenTicketsWidgetData
                      const values = [
                        ...Object.values(newObject),
                        ...data.map((dataItem) => Object.values(dataItem)),
                      ];
                      // console.log(values);
                      vueObj.OpenTicketsWidgetData.push(values);
                    });
                  } else {
                    vueObj.isLoading = false;
                  }
                });
              }
              // if((response.data.result.Xaxis != null || response.data.result.Xaxis != "") && (response.data.result.Yaxis != null || response.data.result.yaxis != "")) {
              //   var xCategory = response.data.result.Xaxis;
              //   var yCategory = response.data.result.Yaxis;
              //   vueObj.HeaderData = response.data.result.data;
              //   var categoryArray = [];
              //   var arrAU = [];
              //   $.each(vueObj.HeaderData, function (index, element) {
              //     //arrAU.push([element.NAME, element.VALUE]);
              //     categoryArray.push(element.NAME);
              //     arrAU.push(element.VALUE);
              //   });
              //   vueObj.items = [xCategory, yCategory];
              //   vueObj.OpenTicketsWidgetData = categoryArray.map((listing, index) => ({ listing, count: arrAU[index] }));
              // }
            }
             else
            {
              var xCategory = response.data.result.Xaxis;
              var yCategory = response.data.result.Yaxis;
              vueObj.HeaderData = response.data.result.data;
              var categoryArray = [];
              var arrAU = [];
              $.each(vueObj.HeaderData, function (index, element) {
                categoryArray.push(element.NAME);
                arrAU.push(element.VALUE);
              });
              vueObj.items = [xCategory, yCategory];
              vueObj.OpenTicketsWidgetData = categoryArray.map((listing, index) => ({ listing, count: arrAU[index] }));
            }
            // if (response.data.result.data != null) {
            //     vueObj.OpenTicketsWidgetData = response.data.result.data;
            //     console.log(vueObj.OpenTicketsWidgetData);
            // }
          }
        }
      });
      vueObj.isLoading = false;
    },
    handleFetchData() {
      this.FetchData();
    },
    Increase () {
      
       var vm = this;
       vm.PageNumber++;
       vm.PageCalculate = vm.PageNumber * 10;
         
    },
    Decrease () {
      
       var vm = this;
       vm.PageNumber--;
         
    },
  },
};
</script>
<style scoped>
.scrollable {
  overflow-y: auto; /* Enable vertical scrollbar */
}

.table-container {
  position: relative;
}
.view-report{
  margin-left: 10px;
  margin-top: 10px;
  text-decoration: underline;
  color: #1f77b4;
  font-weight: 400;
  cursor: pointer;
  float: left;
}
</style>
